import React, { Component } from 'react'
import { Helmet } from 'react-helmet';

import { IconButton, Snackbar, DialogTitle, DialogActions, Button, FormControlLabel, Checkbox} from '@material-ui/core';
import FavoriteIcon from '@material-ui/icons/Favorite';
import FavoriteBorderIcon from '@material-ui/icons/FavoriteBorder';
import logo from '../media/mainlogo.png';
import DelayLink from './DelayLink';
import Slider from 'react-slick';

import portfolio from '../media/portfolio.png';

const axios = require('axios');
const categories = ['Weddings', 'Sports Events', 'Modeling', 'Landscape', 'Graduations', 'Family Portraits', 'Covid-19'];

let scrolling = false;
const rightAndLeft = (e) => {
    if (e.key === 'ArrowLeft') {
        document.querySelector('.slick-prev').click();
    } else if (e.key === 'ArrowRight') {
        document.querySelector('.slick-next').click();
    } else if (e.deltaY < 0 && !scrolling) {
        document.querySelector('.slick-next').click();
        scrolling = true;
        setTimeout(() => scrolling = false, 750);
    } else if (e.deltaY > 0 && !scrolling) {
        document.querySelector('.slick-prev').click();
        scrolling = true;
        setTimeout(() => scrolling = false, 750);
    }
}

export class Portfolio extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
            renderedPhotos: [],
            favorites: [], 
            notification: '',
            open: false,

            filters: [['Weddings', false], ['Sports Events', false], ['Modeling', false], ['Landscape', false], ['Graduations', false], ['Family Portraits', false], ['Covid-19', false]],
            formattedCategories: []
        }
    }
    
    componentDidMount() {
        this.reRender();
        document.removeEventListener('keydown', rightAndLeft);
        document.removeEventListener('wheel', rightAndLeft);
        document.addEventListener('keydown', rightAndLeft, {passive: true})
        document.addEventListener('wheel', rightAndLeft, {passive: true})
    }

    componentWillUnmount() {
        document.removeEventListener('keydown', rightAndLeft)
        document.removeEventListener('wheel', rightAndLeft)
    }

    updateFilters(index) {
        let filters = [...this.state.filters];
        filters[index][1] = !filters[index][1];
        this.setState({filters}, () => this.reRender());
    }

    removeFavorite(image) {
        axios.delete('/favorites', {data: {
            id: image
        }}).then(res => {
            if (res.status === 200) {
                setTimeout(() => this.reRender(), 300)
                setTimeout(() => this.setState({notification: 'Removed image to Favorites'}), 750);
                setTimeout(() => this.setState({notification: ''}), 1750)
            }
        })
    }

    addFavorite(image) {
        axios.post('/favorites', {id: image}).then(res => {
            if (res.status === 200) {
                setTimeout(() => this.reRender(), 300)
                setTimeout(() => this.setState({notification: 'Added image to Favorites'}), 750);
                setTimeout(() => this.setState({notification: ''}), 1750)
            }
        })
    }

    reRender() {
        axios.get('/user').then(res => {
            this.setState({favorites: res.data.favorites === undefined ? [] : res.data.favorites, 
                formattedCategories: 
                this.state.filters.map((category, index) => {
                    return (<FormControlLabel key={category[0]}
                    control={
                    <Checkbox
                        checked={this.state.filters[index][1]}
                        onChange={() => this.updateFilters(index)}
                        name={category[0]}
                        color="primary"
                    />
                    }
                    label={category[0]}
                />);
                })}, () => {
                let formattedImages = [];
                let imageCounter = 0;
        
                const noneSelected = !this.state.filters.map(filter => filter[1]).includes(true);

                for (let i = 0; i < Object.keys(this.props.captions).length; i++) {
                    if (this.state.filters[Object.values(this.props.captions)[i][1]][1] || noneSelected) {
                        const image = Object.keys(this.props.captions)[i];
                        formattedImages.push(
                            <div className={`image`} key={image}>
                                <img src={image} className={`formattedImage ${image.naturalWidth < image.naturalHeight ? 'portrait' : 'landscape'}`}/>
                                <div className="captionOverlay">
                                    <p>{this.props.captions[image][0]},<br/> <i>{categories[this.props.captions[image][1]]}</i></p>
                                </div>
                                <IconButton className={`favoriteIcon ${!this.state.favorites.includes(image) && 'notFavoritedIcon'}`} onClick={() => this.state.favorites.includes(image) ? this.removeFavorite(image) : this.addFavorite(image)}>
                                    {this.state.favorites.includes(image) ? <FavoriteIcon color="error"/> : <FavoriteBorderIcon color="error" />}
                                </IconButton>
                            </div>
                        )
                    }
                    imageCounter++;
                }
        
                this.setState({formattedImages})
            })
        }).catch(err => {
            this.setState({favorites: [], formattedCategories: 
                this.state.filters.map((category, index) => {
                    return (<FormControlLabel key={category[0]}
                    control={
                    <Checkbox
                        checked={this.state.filters[index][1]}
                        onChange={() => this.updateFilters(index)}
                        name={category[0]}
                        color="primary"
                    />
                    }
                    label={category[0]}
                />);
                })}, () => {
                let formattedImages = [];
                let imageCounter = 0;
                
                const noneSelected = !this.state.filters.map(filter => filter[1]).includes(true);
        
                for (let i = 0; i < Object.keys(this.props.captions).length; i++) {
                    if (this.state.filters[Object.values(this.props.captions)[i][1]][1] || noneSelected) {
                        const image = Object.keys(this.props.captions)[i];
                        formattedImages.push(
                            <div className="image" key={image}>
                                <img src={image} className={`formattedImage ${image.naturalWidth < image.naturalHeight ? 'portrait' : 'landscape'}`}/>
                                <div className="captionOverlay">
                                    <p>{this.props.captions[image][0]},<br/> <i>{categories[this.props.captions[image][1]]}</i></p>
                                </div>
                                <IconButton className={`favoriteIcon ${!this.state.favorites.includes(image) && 'notFavoritedIcon'}`} onClick={() => this.props.loggedIn ? (this.state.favorites.includes(image) ? this.removeFavorite(image) : this.addFavorite(image)) : this.setState({open: true})}>
                                    {this.state.favorites.includes(image) ? <FavoriteIcon color="error"/> : <FavoriteBorderIcon color="error" />}
                                </IconButton>
                            </div>
                        )
                    }
                    imageCounter++;
                }
        
                this.setState({formattedImages})
            })
        })
    }

    render() {
        return (
            <div className="Landing">
                <Helmet>
                    <title>Portfolio - Thomas Yarnell Photography</title>
                </Helmet>
                <div className="portfolio">
                        <div className="portfolioTitle">
                            <img src={portfolio} title="portfolio"/>
                        </div>
                        <div className="images">
                            <Slider dots infinite speed={750} lazyLoad useTransform={false}>
                                {this.state.formattedImages}
                            </Slider>
                        </div>
                        <div className="filters">
                            <p>Categories</p>
                            {this.state.formattedCategories}
                        </div>
                </div>
                <Snackbar
                    open={this.state.notification.length > 0}
                    anchorOrigin={{vertical: 'top', horizontal: 'center'}}
                    message={this.state.notification}
                    autoHideDuration={1500}
                />
                <div className={`shopPopUp ${this.state.open ? 'visible' : 'hidden'}`} onClick={() => this.setState({open: false})}>
                    <div className="popContainer">
                        <img src={logo} />
                        <DialogTitle>Sign Up To . . .</DialogTitle>
                        <p>Keep Track of Your Bookings</p>
                        <p>Save Your Favorites</p>
                        <p>Checkout Items</p>
                        <DialogActions className="buttons">
                            <DelayLink to="/signup" delay={600} onDelayStart={() => {this.props.runLoader({toggle: false, time: 750})}}><Button variant="contained" color="secondary">Sign Up</Button></DelayLink>
                            <DelayLink to="/login" delay={600} onDelayStart={() => {this.props.runLoader({toggle: false, time: 750})}}><Button onClick={this.toLogIn} variant="contained" color="primary">Log In</Button></DelayLink>
                        </DialogActions>
                    </div>
                </div>
            </div>
        )
    }
}

export default Portfolio
