import React, { Component } from 'react'

import { Button, Paper, TextField } from '@material-ui/core';
import axios from 'axios';

const pwRegex = /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{8,}$/;

export class ResetPassword extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
            password: '',
            confirmPassword: '',
            pwValid: true,

            errors: [],
            success: [],
        }

        this.updateConfirmPassword = this.updateConfirmPassword.bind(this);
        this.updatePassword = this.updatePassword.bind(this);
        this.changePassword = this.changePassword.bind(this);
    }

    changePassword(event) {
        let pwValid = false;
        let confValid = false;
        let errors = [];

        if (pwRegex.test(this.state.password)) {
            pwValid = true;
        } else {
            errors.push(<p>Password does not meet complexity requirements.</p>);
        }

        if (this.state.password === this.state.confirmPassword) {
            confValid = true;
        } else {
            errors.push(<p>Passwords do not match.</p>);
        }

        if (pwValid && confValid) {
            let url = '';
            if (window.location.href.substring(9,10) === 'w') {
                url = window.location.href.substring(29);
            } else {
                url = window.location.href.substring(25);
            }
            axios.patch(url, {
                password: btoa(this.state.password)
            }).then(res => {
                if (res.status === 200) {
                    this.setState({errors: [], pwValid: pwValid || confValid, success: 'Password has successfully been reset.', password: '', confirmPassword: ''})
                } else {
                    this.setState({errors: [<p>Server error. Please try again later.</p>], pwValid: pwValid || confValid, success: ''})
                }
            }).catch(err => {
                if (err.response.status === 408 || err.response.status === 412) {
                    this.setState({errors: [<p>Reset link is invalid, please request another email.</p>], pwValid: pwValid || confValid, success: ''})
                } else {
                    this.setState({errors: [<p>Server error. Please try again later.</p>], pwValid: pwValid || confValid, success: ''})
                }
            })
        } else {
            this.setState({errors: errors, pwValid: pwValid || confValid, success: ''})
        }
    }

    updatePassword(event) {
        this.setState({password: event.target.value, pwValid: true});
    }
    
    updateConfirmPassword(event) {
        this.setState({confirmPassword: event.target.value,  confValid: true});
    }

    render() {
        return (
            <div className="ResetPassword">
                <h1 className="title">Reset Password</h1>
                <Paper className="container" elevation={5}>
                    {this.state.errors.length > 0 && <div className="errors">{this.state.errors}</div>}
                    <div className="input">
                        <TextField error={!this.state.pwValid} value={this.state.password} onChange={this.updatePassword} fontWeight={600} label="Password" type="password" variant="outlined" />
                        <TextField error={!this.state.pwValid} value={this.state.confirmPassword} onChange={this.updateConfirmPassword} fontWeight={600} label="Confirm Password" type="password" variant="outlined" />
                        <Button variant="contained" color="secondary" onClick={this.changePassword}>Reset Password</Button>
                    </div>
                    {this.state.success.length > 0 && <div className="success">{this.state.success}</div>}
                </Paper>
            </div>
        )
    }
}

export default ResetPassword
