import React, { Component } from 'react'
import { Button, TextField, Snackbar, Dialog, DialogTitle, DialogActions, FormControl, InputLabel, Select, MenuItem, Grid, Typography } from '@material-ui/core';
import { KeyboardDatePicker } from "@material-ui/pickers";
import Autocomplete from '@material-ui/lab/Autocomplete';
import LocationOnIcon from '@material-ui/icons/LocationOn';

import parse from 'autosuggest-highlight/parse';
import throttle from 'lodash/throttle';

import EditIcon from '@material-ui/icons/Edit';
import DeleteIcon from '@material-ui/icons/Delete';
import DelayLink from '../DelayLink';

import nobook from '../../media/nobook.png'

const axios = require('axios');

const emailRegex = /^(([^<>()[\].,;:\s@"]+(.[^<>()[\].,;:\s@"]+)*)|(".+"))@(([^<>()[\].,;:\s@"]+\.)+[^<>()[\].,;:\s@"]{2,})$/i;
const nameRegex = /^[ a-zA-Z\-]+$/;
const phoneRegex = /^[\d\-() ]{0,25}$/;
const phoneFinalRegex = /^[0-9]{10,11}$/;
let autocompleteService;

export class Booked extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
            notification: '',
            deleteId: '',
            editBooking: false,

            bookings: [],
            rawBookings: [],

            id: '',
            firstName: '',
            lastName: '',
            email: '',
            phone: '',
            location: '',
            locationObject: '',
            date: '',
            oldDate: '',
            eventId: '',
            unavailableDates: [],
            timeDisabled: [false, false, false],
            time: '',
            type: '',
            package: '',
            notes: '',
            loaded: false,
            locationOptions: [],
            locationChanged: 0,

            firstNameValid: true,
            lastNameValid: true,
            phoneValid: true,
            emailValid: true,
            locationValid: true,
            dateValid: true,
            timeValid: true,
            typeValid: true,
            packageValid: true,
            errors: [],
            success: ''
        }

        if (typeof window !== 'undefined' && !this.state.loaded) {
            if (!document.querySelector('#google-maps1')) {
                this.loadScript(
                'https://maps.googleapis.com/maps/api/js?key=AIzaSyBhfHa0hrP9vfTJ5fk94tfSPSczil3ElgU&libraries=places',
                document.querySelector('head'),
                'google-maps1',
                );
            }
            this.setState({loaded: true})
        }

        this.updateEmail = this.updateEmail.bind(this);
        this.updateFirstName = this.updateFirstName.bind(this);
        this.updateLastName = this.updateLastName.bind(this);
        this.updatePhone = this.updatePhone.bind(this);
        this.updateLocationOptions = this.updateLocationOptions.bind(this);
        this.updateLocationOptions = throttle(this.updateLocationOptions, 500);
        this.updateLocation = this.updateLocation.bind(this);
        this.updateDate = this.updateDate.bind(this);
        this.updateTime = this.updateTime.bind(this);
        this.updateType = this.updateType.bind(this);
        this.updatePackage = this.updatePackage.bind(this);
        this.updateNotes = this.updateNotes.bind(this);
        this.updateBooking = this.updateBooking.bind(this);

        this.getFormattedDate = this.getFormattedDate.bind(this);
        this.deleteBooking = this.deleteBooking.bind(this);
    }

    loadScript(src, position, id) {
        if (!position) {
          return;
        }
      
        const script = document.createElement('script');
        script.setAttribute('async', '');
        script.setAttribute('id', id);
        script.src = src;
        position.appendChild(script);
    }

    componentDidMount() {
        setTimeout(() => autocompleteService = {current: new window.google.maps.places.AutocompleteService()}, 1500);
        this.reRender();
    }
    
    editOptions(booking) {
        if (Date.now() + 1000*60*60*40 < new Date(booking.date).getTime()) {
            axios.get('/booking').then(res => {
                let preUnavailableDates = [];
                preUnavailableDates = res.data.map(date => {
                    return [date.slice(0,10), date.slice(11, 16), date];
                })
                if (new Date().getHours() >= 9 && new Date().getHours() < 17) {
                    const getRidOfTime = new Date(new Date().getTime() + 1000*60*60*24*2).toISOString().slice(0,11) + '09:00:00.000Z';
                    preUnavailableDates.push([getRidOfTime.slice(0,10), getRidOfTime.slice(11,16), getRidOfTime]);
                }
                if (new Date().getHours() >= 13 && new Date().getHours() < 17) {
                    const getRidOfTime = new Date(new Date().getTime() + 1000*60*60*24*2).toISOString().slice(0,11) + '13:00:00.000Z';
                    preUnavailableDates.push([getRidOfTime.slice(0,10), getRidOfTime.slice(11,16), getRidOfTime]);
                }
                this.setState({unavailableDates: preUnavailableDates}, () => {
                    let combined = this.state.unavailableDates.map(pair => pair[2]);
                    this.setState({editBooking: true, id: booking._id, eventId: booking.eventId, oldDate: booking.date, firstName: booking.first_name, lastName: booking.last_name, phone: booking.phone_num, email: booking.email, date: booking.date, location: booking.location, time: booking.date.slice(11,19), type: booking.type, package: booking.package, notes: booking.notes, timeDisabled: [(combined.includes(booking.date.slice(0,11) + '09:00:00.000Z')) && booking.date[12] !== '9', (combined.includes(booking.date.slice(0,11) + '13:00:00.000Z')) && booking.date[12] !== '3', (combined.includes(booking.date.slice(0,11) + '17:00:00.000Z')) && booking.date[12] !== '7']}, () => {  
                        this.disableDates(new Date(this.state.date));
                    });
                });
            }).catch(err => {
            })
        } else {
            this.setState({notification: 'Cannot edit booking 48 hours beforehand.'})
            setTimeout(() => this.setState({notification: ''}), 4000);
        }
    }

    deleteBooking(id) {
        this.setState({confirmDelete: true, deleteId: id})
    }

    actuallyDelete() {
        if (new Date().getTime() + 1000*60*60*41 > new Date(this.state.rawBookings.find(element => element._id === this.state.deleteId).date).getTime()) {
            this.setState({notification: 'Cannot delete booking 48 hours beforehand.', confirmDelete: false, deleteId: ''})
            setTimeout(() => this.setState({notification: ''}), 4000);
        } else {
            axios.delete(`/bookings/${this.state.deleteId}`).then(res => {
                this.reRender();
                this.setState({notification: 'Succesfully deleted booking.'})
                setTimeout(() => this.setState({notification: ''}), 4000);
            }).catch(err => {})
        }
    }

    getFormattedDate(date) {
        var year = date.getFullYear();
        
        var month = (1 + date.getMonth()).toString();
        month = month.length > 1 ? month : '0' + month;
        
        var day = date.getDate().toString();
        day = day.length > 1 ? day : '0' + day;
        
        var time = '';
        if (parseInt(date.toISOString().split(' ')[0].substring(11, 13)) > 12) {
            if (parseInt(date.toISOString().split(' ')[0].substring(11, 13)) - 12 > 9) {
                time = ((parseInt(date.toISOString().split(' ')[0].substring(11, 13)) - 12) + date.toISOString().split(' ')[0].substring(14, 16) + 'PM'); 
            } else {
                time = ('0' + (parseInt(date.toISOString().split(' ')[0].substring(11, 13)) - 12) + date.toISOString().split(' ')[0].substring(13, 16) + 'PM'); 
            }
        } else {
            time = (date.toISOString().split(' ')[0].substring(11, 16) + 'AM');
        }

        return month + '/' + day + '/' + year + ' ' + time;
    }

    disableDates(day) {
        let dates = this.state.unavailableDates.map(pair => pair[0])
        let combined = this.state.unavailableDates.map(pair => pair[2])
        if (this.state.unavailableDates !== undefined && dates.includes(day.toISOString().slice(0,10))) {
            if ((combined.includes(day.toISOString().slice(0,11) + '09:00:00.000Z')) && (combined.includes(day.toISOString().slice(0,11) + '13:00:00.000Z')) && (combined.includes(day.toISOString().slice(0,11) + '17:00:00.000Z'))) {
                if (new Date(this.state.date).toISOString().slice(0,10) === day.toISOString().slice(0,10)) {
                    this.setState(prevState => {return {date: new Date(new Date(prevState.date).getTime() + 1000*60*60*24)}})
                }
                return true;
            }
        } else {
            return false;
        }
    }

    updateEmail(event) {
        this.setState({email: event.target.value, emailValid: true});
    }
    updateFirstName(event) {
       (event.target.value === '' || event.target.value.length === 0 || nameRegex.test(event.target.value)) && this.setState({firstName: event.target.value, firstNameValid: true});
    }

    updateLastName(event) {
        (event.target.value === '' || event.target.value.length === 0 || nameRegex.test(event.target.value)) && this.setState({lastName: event.target.value, lastNameValid: true});
    }

    updatePhone(event) {
        const value = event.target.value;
        if ((phoneRegex.test(value) && value.split(" ").length - 1 <= 4) || value.length === 0) {
            this.setState({phone: value, phoneValid: true});  
        }
    }


    updateLocation(place) {
        this.setState({locationObject: place, locationValid: true});
    }

    updateDate(date) {
        if (date !== null && date._isValid) {
            let combined = this.state.unavailableDates.map(pair => pair[2]);
            this.setState({date: date.toISOString(), dateValid: true, timeDisabled: [(combined.includes(date.toISOString().slice(0,11) + '09:00:00.000Z')), (combined.includes(date.toISOString().slice(0,11) + '13:00:00.000Z')), (combined.includes(date.toISOString().slice(0,11) + '17:00:00.000Z'))]});
        } else {
            this.setState({date: '', dateValid: false})
        }
    }

    updateTime(event) {
        this.setState({time: event.target.value, timeValid: true})
    }

    updateType(event) {
        this.setState({type: event.target.value, typeValid: true})
    }

    updatePackage(event) {
        this.setState({package: event.target.value, packageValid: true})
    }

    updateNotes(event) {
        this.setState({notes: event.target.value})
    }

    updateLocationOptions(place) {
        if (autocompleteService !== undefined) {
            if (!autocompleteService.current && window.google) {
                autocompleteService.current = new window.google.maps.places.Autocomplete(document.getElementById('google-maps1'));
            }
            if (place !== '') {
                autocompleteService.current.getPlacePredictions({input: place, types: ['geocode'], country: ['us']}, (outputs) => {
                    this.setState(prevState => {return ({locationOptions: outputs, location: place, locationChanged: prevState.locationChanged + 1})})  
                })
            } else {
                this.setState(prevState => {return {locationOptions: [], location: '', locationChanged: prevState.locationChanged + 1}})
            }
        }
    }

    updateBooking() {
        let firstNameValid = false;
        let lastNameValid = false;
        let phoneValid = false;
        let emailValid = false;
        let locationValid = false;
        let dateValid = false;
        let timeValid = false;
        let typeValid = false;
        let packageValid = false;
        let errors = [];

        if (this.state.firstName.length > 1) {
            firstNameValid = true;
        } else {
            errors.push(<p>Please enter a first name.</p>);
        }

        if (this.state.lastName.length > 1) {
            lastNameValid = true;
        } else {
            errors.push(<p>Please enter a last name.</p>);
        }

        if (emailRegex.test(this.state.email)) {
            emailValid = true;
        } else {
            errors.push(<p>Please enter a valid email.</p>);
        }

        if (phoneRegex.test(this.state.phone) && this.state.phone.split(" ").length - 1 <= 4 && this.state.phone.length > 0 && phoneFinalRegex.test(this.state.phone.replace(/-/g, "").replace(/[()]/g, "").replace(/\s/g, ""))) {
            phoneValid = true;  
        } else {
            errors.push(<p>Please enter a valid phone number.</p>);
        }

        if (this.state.date !== null && this.state.date.length > 0) {
            dateValid = true;
        } else {
            errors.push(<p>Please enter a date.</p>);
        }

        if (this.state.locationChanged <= 1 || (this.state.locationObject !== null && this.state.locationObject.description !== undefined && this.state.locationObject.description !== null && this.state.locationObject.description.length > 0)) {
            locationValid = true;
        } else {
            errors.push(<p>Please enter an address.</p>);
        }

        if (this.state.time !== null && this.state.time.length > 0) {
            timeValid = true;
        } else {
            errors.push(<p>Please select a time.</p>);
        }

        if (this.state.type !== null && this.state.type.length > 0) {
            typeValid = true;
        } else {
            errors.push(<p>Please select a category.</p>);
        }

        if (this.state.package !== null && this.state.package.length > 0) {
            packageValid = true;
        } else {
            errors.push(<p>Please select a package.</p>);
        }

        if (emailValid && firstNameValid && lastNameValid && phoneValid && locationValid && dateValid && timeValid && typeValid && packageValid) {
            axios.patch(`/bookings/${this.state.id}`, {
                first_name: this.state.firstName,
                last_name: this.state.lastName,
                email: this.state.email,
                phone_num: this.state.phone,
                location: this.state.locationChanged <= 1 ? this.state.location : this.state.locationObject.description,
                date: (this.state.date.slice(0,11) + this.state.time + '.000Z'),
                old_date: this.state.oldDate,
                type: this.state.type,
                package: this.state.package,
                notes: this.state.notes,
                eventId: this.state.eventId,
            }).then(res => {
                if (res.status === 200 || res.status === 304) {
                    this.setState({notification: 'loading...'})
                    axios.get('/booking').then(res => {
                        let preUnavailableDates = [];
                        preUnavailableDates = res.data.map(date => {
                            return [date.slice(0,10), date.slice(11, 16), date];
                        })
                        if (new Date().getHours() >= 9 && new Date().getHours() < 17) {
                            const getRidOfTime = new Date(new Date().getTime() + 1000*60*60*24*2).toISOString().slice(0,11) + '09:00:00.000Z';
                            preUnavailableDates.push([getRidOfTime.slice(0,10), getRidOfTime.slice(11,16), getRidOfTime]);
                        }
                        if (new Date().getHours() >= 13 && new Date().getHours() < 17) {
                            const getRidOfTime = new Date(new Date().getTime() + 1000*60*60*24*2).toISOString().slice(0,11) + '13:00:00.000Z';
                            preUnavailableDates.push([getRidOfTime.slice(0,10), getRidOfTime.slice(11,16), getRidOfTime]);
                        }
                        this.setState({unavailableDates: preUnavailableDates}, () => {
                            let combined = this.state.unavailableDates.map(pair => pair[2]);
                            this.setState({timeDisabled: [(combined.includes(this.state.date.slice(0,11) + '09:00:00.000Z')), (combined.includes(this.state.date.slice(0,11) + '13:00:00.000Z')), (combined.includes(this.state.date.slice(0,11) + '17:00:00.000Z'))]});
                            this.setState({errors: errors, emailValid, firstNameValid, lastNameValid, phoneValid, locationValid, dateValid, timeValid, typeValid, packageValid,
                                firstName: '',
                                lastName: '',
                                email: '',
                                phone: '',
                                location: '',
                                locationOptions: [],
                                locationObject: '',
                                locationChanged: 0,
                                time: '',
                                type: '',
                                package: '',
                                notes: '',
                                notification: 'Succesfully edited booking.',
                                editBooking: false
                            })
                            setTimeout(() => this.setState({notification: ''}), 4000)
                            this.props.runLoader({toggle: false, time: 750, type: 'dashboard'})
                            this.reRender();
                        });
                    }).catch(err => {
                    })
                } else {
                    errors = [<p>Error booking, try again later.</p>]
                    this.setState({notification: '', errors: errors, emailValid, firstNameValid, lastNameValid, phoneValid, locationValid, dateValid, timeValid, typeValid, packageValid, success: ''})
                }
            }).catch(err => {
                errors = [<p>Error booking, try again later.</p>]
                this.setState({notification: '', errors: errors, emailValid, firstNameValid, lastNameValid, phoneValid, locationValid, dateValid, timeValid, typeValid, packageValid, success: ''})
            })
        } else {
            this.setState({notification: '', errors: errors, emailValid, firstNameValid, lastNameValid, phoneValid, locationValid, dateValid, timeValid, typeValid, packageValid, success: ''})
        }
    }

    reRender() {
        axios.get('/user/bookings')
            .then(res => {
                if (res.status === 200 || res.status === 304) {
                    res.data.sort((a, b) => {
                        if (new Date(a.date) < new Date(b.date)) {
                            return -1;
                        } else {
                            return 1; 
                        }
                    })

                    const bookingsMapped = res.data.map(booking => {
                        var urlLocation = booking.location.replace(" ", "+");
                        const staticMapLink = "https://maps.googleapis.com/maps/api/staticmap?&maptype=hybrid&size=600x400&markers=color:red%7C" + urlLocation + "&zoom=18&key=AIzaSyAXnO5_29oeGqNIchVstCYS29QCI-wBJ0Q";
                        return (
                        <div className="indivBooking" key={booking._id}>
                            <div className="icons">
                                    <EditIcon onClick={() => this.editOptions(booking)}/>
                                    <DeleteIcon onClick={() => this.deleteBooking(booking._id)}/>
                            </div>
                            <div className="topRow">
                                <p><span>Date: </span>{this.getFormattedDate(new Date(booking.date))}</p>
                            </div>
                            <p><span>Location: </span>{booking.location}</p>
                            <p><span>Type: </span>{booking.type}</p>
                            <p><span>Package:</span> {booking.package}</p>
                            <p className="notes"><span>Notes: </span>{booking.notes}</p>
                            <a href={`https://www.google.com/maps/place/${urlLocation}`} target="_blank" rel="noreferrer"><img src={staticMapLink} className="map" alt="map"/></a>
                        </div>
                        );
                    })
                    this.setState({rawBookings: res.data, bookings: bookingsMapped, confirmDelete: false})
                }
            }).catch(err => {})
    }

    render() {
        return (
            <div className="Booked">
                <div className="container"><Button variant="contained" className="newBooking"><DelayLink delay={600} to="/bookings" onDelayStart={() => this.props.runLoader({toggle: false, time: 750})}>Create New Booking</DelayLink></Button></div>
                {this.state.bookings.length > 0 ? this.state.bookings : <div className="emptyPanel"><img className="emptyPanel" src={nobook} /><h2>Nothing Booked!</h2></div>}
                <Snackbar
                    open={this.state.notification.length > 0}
                    anchorOrigin={{vertical: 'top', horizontal: 'center'}}
                    message={this.state.notification}
                    autoHideDuration={4000}
                />
                <Dialog className="deleteDialog" open={this.state.confirmDelete}>
                    <DialogTitle >Delete Booking?</DialogTitle>
                    <DialogActions className="buttons">
                    <Button onClick={() => {this.setState({confirmDelete: false, deleteId: ''})}} variant="contained" color="secondary">
                        No
                    </Button>
                    <Button onClick={() => this.actuallyDelete()} variant="contained" color="primary">
                        Yes
                    </Button>
                    </DialogActions>
                </Dialog>
                <Dialog className="editBooking" open={this.state.editBooking}>
                    {this.state.errors.length > 0 && <div className="errors">{this.state.errors}</div>}
                    <DialogTitle>Edit Booking</DialogTitle>
                    <div className="input">
                        <TextField className="name" error={!this.state.firstNameValid} value={this.state.firstName} onChange={this.updateFirstName} fontWeight={600} label="First Name" variant="outlined" />
                        <TextField className="name" error={!this.state.lastNameValid} value={this.state.lastName} onChange={this.updateLastName} fontWeight={600} label="Last Name" variant="outlined" />
                        <TextField className="email" error={!this.state.emailValid} value={this.state.email} onChange={this.updateEmail} fontWeight={600} label="Email" type="email" variant="outlined" />
                        <TextField className="phone" error={!this.state.phoneValid} value={this.state.phone} onChange={this.updatePhone} fontWeight={600} label="Phone Number" type="tel" variant="outlined"/>
                        <KeyboardDatePicker
                            className="date"
                            error={!this.state.dateValid}
                            autoOk
                            variant="inline"
                            inputVariant="outlined"
                            label="Edit Date"
                            format="MM/DD/yyyy"
                            minDate={new Date().getHours() < 17 ? new Date(Date.now()+2*24*60*60*1000-7*60*60*1000).toISOString() : new Date(Date.now()+3*24*60*60*1000-7*60*60*1000).toISOString()}
                            maxDate={new Date(Date.now()+365*24*60*60*1000)}
                            allowKeyboardControl
                            shouldDisableDate={(day) => this.disableDates(day)}
                            value={this.state.date}
                            onChange={date => this.updateDate(date)}
                        />
                        <FormControl variant="outlined" className="dropdown" error={!this.state.timeValid}>
                            <InputLabel>Edit Time</InputLabel>
                            <Select label="Edit Time" value={this.state.time} onChange={this.updateTime}>
                                <MenuItem disabled={this.state.timeDisabled[0]} value={'09:00:00'}>9:00 AM</MenuItem>
                                <MenuItem disabled={this.state.timeDisabled[1]} value={'13:00:00'}>1:00 PM</MenuItem>
                                <MenuItem disabled={this.state.timeDisabled[2]} value={'17:00:00'}>5:00 PM</MenuItem>
                            </Select>
                        </FormControl>
                        <Autocomplete
                            defaultValue={this.state.location}
                            autoComplete
                            autoHighlight
                            id="google-maps1"
                            options={this.state.locationOptions}
                            getOptionLabel={(option) => typeof option === 'string' ? option: (option.description)}
                            className="location"
                            includeInputInList
                            renderInput={(params) => (<TextField {...params} label="Address" variant="outlined" fullWidth error={!this.state.locationValid && 'inputError'}/>)}
                            onInputChange={(event, place) => this.updateLocationOptions(place)}
                            onChange={(event, place) => this.updateLocation(place)}
                            renderOption={(option) => {
                                const matches = option.structured_formatting.main_text_matched_substrings;
                                const parts = parse(
                                    option.structured_formatting.main_text,
                                    matches.map((match) => [match.offset, match.offset + match.length]),
                                );
                        
                                return (
                                    <Grid container alignItems="center">
                                    <Grid item>
                                        <LocationOnIcon />
                                    </Grid>
                                    <Grid item xs>
                                        {parts.map((part, index) => (
                                        <span key={index} style={{ fontWeight: part.highlight ? 700 : 400 }}>
                                            {part.text}
                                        </span>
                                        ))}
                        
                                        <Typography variant="body2" color="textSecondary">
                                        {option.structured_formatting.secondary_text.substring(0, option.structured_formatting.secondary_text.length-5)}
                                        </Typography>
                                    </Grid>
                                    </Grid>
                                );
                                }}
                        />
                        <FormControl variant="outlined" className="dropdown" error={!this.state.typeValid}>
                            <InputLabel>Edit Category</InputLabel>
                            <Select label="Edit Category" value={this.state.type} onChange={this.updateType}>
                            <MenuItem value={'Weddings'}>Weddings</MenuItem>
                            <MenuItem value={'Sports Events'}>Sports Events</MenuItem>
                            <MenuItem value={'Modeling'}>Modeling</MenuItem>
                            <MenuItem value={'Landscape'}>Landscape</MenuItem>
                            <MenuItem value={'Graduations'}>Graduations</MenuItem>
                            <MenuItem value={'Family Portraits'}>Family Portraits</MenuItem>
                            <MenuItem value={'Other'}>Other</MenuItem>
                            </Select>
                        </FormControl>
                        <FormControl variant="outlined" className="dropdown" error={!this.state.packageValid}>
                            <InputLabel>Edit Package</InputLabel>
                            <Select label="Edit Package" value={this.state.package} onChange={this.updatePackage}>
                                <MenuItem value={'Standard'}>Standard</MenuItem>
                                <MenuItem value={'Premium'}>Premium</MenuItem>
                                <MenuItem value={'Exclusive'}>Exclusive</MenuItem>
                            </Select>
                        </FormControl>
                        <TextField
                            className="notes"
                            label="Additional Notes"
                            multiline
                            rows={3}
                            variant="outlined"
                            value={this.state.notes}
                            onChange={this.updateNotes}
                        />
                    </div>
                    <DialogActions className="buttons">
                    <Button onClick={() => this.setState({editBooking: false, errors: [], locationChanged: 0, locationValid: true, dateValid: true, timeValid: true, typeValid: true, packageValid: true, firstNameValid: true, lastNameValid: true, phoneValid: true, emailValid: true})} variant="contained" color="secondary">
                        Cancel
                    </Button>
                    <Button onClick={this.updateBooking} variant="contained" color="primary">
                        Update Booking
                    </Button>
                    </DialogActions>
                    {this.state.success.length > 0 && <div className="success animate__animated animate_fadeIn">{this.state.success}</div>}
                </Dialog>
            </div>
        )
    }
}

export default Booked
