import React, { Component } from 'react'
import { Button, Snackbar, Dialog, DialogTitle, DialogActions, IconButton } from '@material-ui/core';

import RemoveShoppingCartIcon from '@material-ui/icons/RemoveShoppingCart';
import FavoriteIcon from '@material-ui/icons/Favorite';
import FavoriteBorderIcon from '@material-ui/icons/FavoriteBorder';
import AddIcon from '@material-ui/icons/Add';
import RemoveIcon from '@material-ui/icons/Remove';
import ArrowUpwardIcon from '@material-ui/icons/ArrowUpward';
import ArrowDownwardIcon from '@material-ui/icons/ArrowDownward';

import DelayLink from '../DelayLink';

import nocart from '../../media/nocart.png';

const axios = require('axios');
const formatter = new Intl.NumberFormat('en-US', {
    style: 'currency',
    currency: 'USD',
});

export class Booked extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
            notification: '',
            deleteId: '',
            deleteSize: -1, 
            receipt: '',
            
            rawCart: [],
            cart: [],
            totalPrice: 0,    
            onFavorites: [],
        }

        this.deleteCartItem = this.deleteCartItem.bind(this);
        this.actuallyDelete = this.actuallyDelete.bind(this);
        this.changeAmount = this.changeAmount.bind(this);
        this.requestPrices = this.requestPrices.bind(this);
        this.showReceipt = this.showReceipt.bind(this);
    }

    componentDidMount() {
        this.requestPrices();
    }
    
    deleteCartItem(id, size) {
        this.setState({confirmDelete: true, deleteId: id, deleteSize: size})
    }

    actuallyDelete() {
        axios.delete('/shop', {
            data: {
                id: this.state.deleteId,
                size: this.state.deleteSize,
                quantity: 999
            }
        }).then(res => {
            if (res.status === 200) {
                setTimeout(() => this.requestPrices(), 1200);
                this.setState({notification: 'Removed item from Cart', confirmDelete: false})
                setTimeout(() => this.setState({notification: ''}), 4000)
                setTimeout(() => this.props.runLoader({toggle: false, time: 750, type: 'dashboard'}), 1000);
            } else {
            }
        }).catch(err => {

        })
    }

    changeAmount(item, amount) {
        if (item.quantity + amount <= 10 && item.quantity + amount > 0) {
            axios.post('/shop', {
                id: item.url,
                quantity: amount,
                price: item.price,
                size: item.size,
            }).then(res => {
                if (res.status === 200 || res.status === 304) {
                    this.requestPrices()
                }
            }).catch(err => {});
        } else if (item.quantity + amount > 10) {
            this.setState({notification: 'Maximum of 10 per print reached.'})
            setTimeout(() => this.setState({notification: ''}), 3000);
        } else {
            this.deleteCartItem(item.url, item.size);
        }
    }

    changeSelection(item, diff) {
        if (diff === 1 && item.size < 3) {
            axios.post('/shop', {
                id: item.url,
                quantity: 0,
                price: item.size === 2 ? (item.price / 1.4 * 2) : item.size === 1 ? item.price / 1.2 * 1.4 : item.price * 1.2,
                size: item.size + 1,
                oldSize: item.size,
            }).then(res => {
                if (res.status === 200 || res.status === 304) {
                    axios.get('/user')
                    this.requestPrices()
                }
            }).catch(err => {});
        } else if (diff === -1 && item.size > 0) {
            axios.post('/shop', {
                id: item.url,
                quantity: 0,
                price: item.size === 3 ? (item.price / 2 * 1.4) : item.size === 2 ? item.price / 1.4 * 1.2 : item.price / 1.2,
                size: item.size - 1,
                oldSize: item.size,
            }).then(res => {
                if (res.status === 200 || res.status === 304) {
                    this.requestPrices()
                }
            }).catch(err => {});
        }
    }

    requestPrices() {
        axios.get('/user')
        .then(res => {
            if (res.status === 200 || res.status === 304) {
                this.setState({onFavorites: res.data.favorites}, () => {
                let totalPrice = 0;
                let cart = [];

                let imageCounter = 0;

                const imagesRendered = () => {
                    return new Promise((resolve, reject) => {
                        (function waitForImages() {
                            if (imageCounter === res.data.cart.length) {
                                return resolve();
                            }
                            setTimeout(waitForImages, 30);
                        })()
                    })
                }

                for (let i = 0; i < res.data.cart.length; i++) {
                    let item = res.data.cart[i];
                    let img = new Image();
                    img.src = item.url;
                    img.onload = () => {
                        if (img.naturalWidth < img.naturalHeight) {
                            item.selections = ['12" x 18"', '16" x 24"', '24" x 36"', '32" x 48"'];
                        } else {
                            item.selections = ['18" x 12"', '24" x 16"', '36" x 24"', '48" x 32"'];
                        }
                        totalPrice += item.price * item.quantity;
                        cart[i] = (
                            <div className={`itemContainer ${img.naturalWidth < img.naturalHeight ? 'portrait' : 'landscape'}`}>
                            <div className="image" key={item.url}>
                                <img className="cartImage" src={item.url}/>
                                <div className="captionOverlay">
                                    <p>{this.props.captions[item.url][0] || 'Photo Print'}</p>
                                </div>
                                <IconButton onClick={() => this.deleteCartItem(item.url, item.size)} className="favoriteIcon">
                                    <RemoveShoppingCartIcon color="error"/>
                                </IconButton>
                                <IconButton onClick={() => this.toggleFavorite(item.url)} className="favoriteIcon cartIcon">
                                    {this.state.onFavorites.includes(item.url) ? <FavoriteIcon color="error"/> : <FavoriteBorderIcon color="error"/>}
                                </IconButton>
                            </div>
                            <div className="priceandquantity">
                                <p>Price: {formatter.format(item.price)}</p>
                                <p>Quantity: <IconButton onClick={() => this.changeAmount(item, -1)}><RemoveIcon/></IconButton> {item.quantity} <IconButton onClick={() => this.changeAmount(item, 1)}><AddIcon/></IconButton></p>
                                <p>Size: <IconButton onClick={() => this.changeSelection(item, -1)}><ArrowDownwardIcon/></IconButton> {item.selections[item.size]} <IconButton onClick={() => this.changeSelection(item, 1)}><ArrowUpwardIcon/></IconButton></p>
                            </div>
                        </div>
                        )
                        imageCounter++;
                    }
                }

                imagesRendered().then(() => {this.setState({rawCart: res.data.cart, cart, totalPrice})})  
                })
            }
        }).catch(err => {})
    }

    toggleFavorite(image) {
        if (!this.state.onFavorites.includes(image)) {
            axios.post('/favorites', {id: image}).then(res => {
                if (res.status === 200 || res.status === 304) {
                    let temp = this.state.onFavorites.splice(this.state.onFavorites.indexOf(image), 1);
                    this.setState({notification: 'Added Print To Favorites', onFavorites: temp})
                    setTimeout(() => this.setState({notification: ''}), 4000)
                    this.props.runLoader({toggle: false, time: 750, type: 'dashboard'})
                    this.requestPrices();
                }
            })
        } else {
            axios.delete('/favorites', {data: {id: image}}).then(res => {
                if (res.status === 200 || res.status === 304) {
                    let temp = this.state.onFavorites.splice(this.state.onFavorites.indexOf(image), 1);
                    this.setState({notification: 'Removed Print From Favorites', onFavorites: temp})
                    setTimeout(() => this.setState({notification: ''}), 4000)
                    this.props.runLoader({toggle: false, time: 750, type: 'dashboard'})
                    this.requestPrices();
                }
            })
        }
    }

    showReceipt() {
        if (this.state.totalPrice <= 0) {
            this.setState({notification: 'Nothing in cart!'})
            setTimeout(() => this.setState({notification: ''}), 4000)
        } else {
            this.setState({receipt: this.state.rawCart.map(item => {
                return (
                <tr className="receiptItem">
                    <td>
                        <img className="receiptImage" src={item.url}/>
                    </td>
                    <td>{this.props.captions[item.url][0] || 'Photo Print'}</td>
                    <td>{item.quantity}</td>
                    <td>{item.selections[item.size]}</td>
                    <td>{formatter.format(item.price)}</td>
                </tr>)
            })})
        }
    }

    showFake() {
        this.setState({notification: 'This is not a real site, only for demonstration.', receipt: ''})
        setTimeout(() => this.setState({notification: ''}), 4000)
    }

    render() {
        return (
            <div className="Booked Favorites Cart">
                <div className="goButtons">
                    <div className="container"><Button variant="contained" className="newBooking"><DelayLink delay={600} to="/shop" onDelayStart={() => this.props.runLoader({toggle: false, time: 750})}>Go To Shop</DelayLink></Button></div>
                    <div className="container"><Button variant="contained" className="newBooking" onClick={this.showReceipt}>Checkout (${this.state.totalPrice.toLocaleString()}.00)</Button></div>
                </div>
                {this.state.cart.length > 0 ? <div className="cartGrid">{this.state.cart}</div> : <div className="emptyPanel"><img className="emptyPanel" src={nocart} /><h2>Nothing in Cart!</h2></div>}
                <Snackbar
                    open={this.state.notification.length > 0}
                    anchorOrigin={{vertical: 'top', horizontal: 'center'}}
                    message={this.state.notification}
                    autoHideDuration={4000}
                />
                <Dialog className="deleteDialog" open={this.state.confirmDelete}>
                    <DialogTitle >Delete item from Cart?</DialogTitle>
                    <DialogActions className="buttons">
                    <Button onClick={() => {this.setState({confirmDelete: false, deleteId: '', deleteSize: -1})}} variant="contained" color="secondary">
                        No
                    </Button>
                    <Button onClick={() => this.actuallyDelete()} variant="contained" color="primary">
                        Yes
                    </Button>
                    </DialogActions>
                </Dialog>
                <Dialog className="receipt" open={this.state.receipt !== ''}>
                    <DialogTitle>Receipt</DialogTitle>
                    <table>
                        <tr>
                            <th>Preview</th>
                            <th>Title</th>
                            <th>Qty. &nbsp;&nbsp;&nbsp;</th>
                            <th>Size</th>
                            <th>Price</th>
                        </tr>
                        {this.state.receipt}
                        <tr className="total">
                            <td>Total:</td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td className="totalPrice">${this.state.totalPrice.toLocaleString()}.00</td>
                        </tr>
                    </table>
                    <DialogActions className="buttons">
                    <Button onClick={() => {this.setState({receipt: ''})}} variant="contained" color="secondary">
                        Cancel
                    </Button>
                    <Button onClick={() => this.showFake()} variant="contained" color="primary">
                        Proceed to Payment
                    </Button>
                    </DialogActions>
                </Dialog>
            </div>
        )
    }
}

export default Booked
