import React, { Component } from 'react'
import {Helmet} from 'react-helmet';
import {withRouter} from "react-router-dom";

import { Button, Paper, TextField } from '@material-ui/core';

import googleLogo from '../media/googleLogo.png';

import DelayLink from './DelayLink';

const axios = require('axios');

const emailRegex = /^(([^<>()[\].,;:\s@"]+(.[^<>()[\].,;:\s@"]+)*)|(".+"))@(([^<>()[\].,;:\s@"]+\.)+[^<>()[\].,;:\s@"]{2,})$/i;
const pwRegex = /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{8,}$/;
const nameRegex = /^[ a-zA-Z\-]+$/;
const phoneRegex = /^[\d\-() ]{0,25}$/;
const phoneFinalRegex = /^[0-9]{10,11}$/;

export class Login extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
            panel: this.props.panel,
            
            firstName: '',
            lastName: '',
            email: '',
            password: '',
            confirmPassword: '',
            phone: '',

            firstNameValid: true,
            lastNameValid: true,
            phoneValid: true,
            emailValid: true,
            pwValid: true,
            confValid: true,
            errors: [],
            success: ''
        }

        this.updateEmail = this.updateEmail.bind(this);
        this.updatePassword = this.updatePassword.bind(this);
        this.updateConfirmPassword = this.updateConfirmPassword.bind(this);
        this.updateFirstName = this.updateFirstName.bind(this);
        this.updateLastName = this.updateLastName.bind(this);
        this.updatePhone = this.updatePhone.bind(this);

        this.login = this.login.bind(this);
        this.signUp = this.signUp.bind(this);
        this.forgot = this.forgot.bind(this);
        this.logInWithGoogle = this.logInWithGoogle.bind(this);
    }

    setPanel(option) {
        this.props.runLoader({toggle: false, time: 750});
        setTimeout(() => {
            this.setState(prevState => {
                if (prevState.panel !== option) {
                    return ({
                        panel: option,
                        firstName: '',
                        lastName: '',
                        email: '',
                        password: '',
                        confirmPassword: '',
                        phone: '',
            
                        firstNameValid: true,
                        lastNameValid: true,
                        phoneValid: true,
                        emailValid: true,
                        pwValid: true,
                        confValid: true,
                        errors: [],
                        success: ''})
                }
            })
        }, 500)
    }

    updateEmail(event) {
        this.setState({email: event.target.value, emailValid: true});
    }

    updatePassword(event) {
        this.setState({password: event.target.value, pwValid: true});
    }
    
    updateConfirmPassword(event) {
        this.setState({confirmPassword: event.target.value,  confValid: true});
    }

    updateFirstName(event) {
       (event.target.value === '' || event.target.value.length === 0 || nameRegex.test(event.target.value)) && this.setState({firstName: event.target.value, firstNameValid: true});
    }

    updateLastName(event) {
        (event.target.value === '' || event.target.value.length === 0 || nameRegex.test(event.target.value)) && this.setState({lastName: event.target.value, lastNameValid: true});
    }

    updatePhone(event) {
        const value = event.target.value;
        if ((phoneRegex.test(value) && value.split(" ").length - 1 <= 4) || value.length === 0) {
            this.setState({phone: value, phoneValid: true});  
        }
    }

    login() {
        let emailValid = false;
        let pwValid = false;

        if (emailRegex.test(this.state.email)) {
            emailValid = true;
        }

        if (pwRegex.test(this.state.password)) {
            pwValid = true;
        }

        if (emailValid && pwValid) {
            axios.post('/login', {
                email: this.state.email,
                password: btoa(this.state.password)
            }).then(res => {
                if (res.status === 200) {
                    this.setState({errors: [], success: 'Logged in! Redirecting to dashboard...', emailValid: true}, () => {
                        setTimeout(() => this.props.runLoader({toggle: false, time: 750}), 750);
                        setTimeout(() => this.props.checkLoggedIn(), 1000);
                    })
                } else {
                    this.setState({emailValid: false, errors: [<p>Please enter a valid email and username.</p>], success: ''})
                }
            }).catch(err => {
                this.setState({emailValid: false, errors: [<p>Please enter a valid email and username.</p>], success: ''})
            })
        } else {
            this.setState({emailValid: emailValid && pwValid, errors: [<p>Please enter a valid email and username.</p>], success: ''})
        }
    }

    signUp() {
        let firstNameValid = false;
        let lastNameValid = false;
        let phoneValid = false;
        let emailValid = false;
        let pwValid = false;
        let confValid = false;
        let errors = [];

        if (this.state.firstName.length > 1) {
            firstNameValid = true;
        } else {
            errors.push(<p>Please enter a first name.</p>);
        }

        if (this.state.lastName.length > 1) {
            lastNameValid = true;
        } else {
            errors.push(<p>Please enter a last name.</p>);
        }

        if (phoneRegex.test(this.state.phone) && this.state.phone.split(" ").length - 1 <= 4 && this.state.phone.length > 0 && phoneFinalRegex.test(this.state.phone.replace(/-/g, "").replace(/[()]/g, "").replace(/\s/g, ""))) {
            phoneValid = true;  
        } else {
            errors.push(<p>Please enter a valid phone number.</p>);
        }

        if (emailRegex.test(this.state.email)) {
            emailValid = true;
        } else {
            errors.push(<p>Please enter a valid email.</p>);
        }

        if (pwRegex.test(this.state.password)) {
            pwValid = true;
        } else {
            errors.push(<p>Password does not meet complexity requirements.</p>);
        }

        if (this.state.password === this.state.confirmPassword) {
            confValid = true;
        } else {
            errors.push(<p>Passwords do not match.</p>);
        }

        if (emailValid && pwValid && confValid && firstNameValid && lastNameValid && phoneValid) {
            this.setState({success: 'Loading...'})
            axios.post('/register', {
                first_name: this.state.firstName,
                last_name: this.state.lastName, 
                email: this.state.email,
                password: btoa(this.state.password),
                phone_num: this.state.phone
            }).then(res => {
                if (res.status === 200) {
                    this.setState({success: 'Successfully created account. Please check your email to activate your account.', errors: [], firstNameValid: true,
                    lastNameValid: true,
                    phoneValid: true,
                    emailValid: true,
                    pwValid: true,
                    confValid: true,
                    firstName: '',
                    lastName: '',
                    phone: '',
                    password: '',
                    confirmPassword: '',
                    email: '',
                    })
                } else {
                    this.setState({errors: [<p>Error creating account. Please try again at another time.</p>], success: '', firstNameValid: true,
                    lastNameValid: true,
                    phoneValid: true,
                    emailValid: true,
                    pwValid: true,
                    confValid: true,})
                }
            }).catch(err => {
                this.setState({errors: [<p>Error creating account. Please try again at another time.</p>], success: '', firstNameValid: true,
                lastNameValid: true,
                phoneValid: true,
                emailValid: true,
                pwValid: true,
                confValid: true,})
            })
        } else {
            this.setState({emailValid: emailValid, pwValid: pwValid, confValid: confValid, firstNameValid: firstNameValid, lastNameValid: lastNameValid, phoneValid: phoneValid, errors: errors, success: ''})
        }
    }

    forgot() {
        let emailValid = false;
        let errors = [];

        if (emailRegex.test(this.state.email)) {
            emailValid = true;
        } else {
            errors.push(<p>Please enter a valid email.</p>);
        }

        if (emailValid) {
            axios.post('/login/forgotpasswd', {
                email: this.state.email,
            }).then(res => {
                this.setState({errors: [], success: 'Please check your email to reset your password. Note: if using only Google sign-in, this will not work.'})
            }).catch(err => {
                this.setState({errors: [], success: 'Please check your email to reset your password. Note: if using only Google sign-in, this will not work.'})
            })
        } else {
            this.setState({errors: errors, emailValid: emailValid})
        }
    }

    logInWithGoogle() {
        // window.location.href="http://localhost:8000/auth/google";
        if (window.location.href[window.location.href.length - 6] === 's') {
            window.location.href = window.location.href.slice(0, -6) + 'auth/google';
        } else {
            window.location.href = window.location.href.slice(0, -5) + 'auth/google';
        }
    }

    render() {
        return (
                this.state.panel === 'login' ?
                <div className="Login">
                    <Helmet>
                        <title>Log In - Thomas Yarnell Photography</title>
                    </Helmet>
                <h1 className="title">Log In</h1>
                <Paper className="container" elevation={5}>
                    {this.state.errors.length > 0 && <div className="errors">{this.state.errors}</div>}
                    <div className="input">
                    <div className="google">
                                <hr />
                                <Button className="googleLogin" onClick={this.logInWithGoogle}><p>Log In with</p><img src={googleLogo} alt="google logo"/></Button>
                                <hr />  
                        </div>
                        <TextField error={!this.state.emailValid} value={this.state.email} onChange={this.updateEmail} fontWeight={600} label="Email" type="email" variant="outlined" />
                        <TextField error={!this.state.emailValid} value={this.state.password} onChange={this.updatePassword} fontWeight={600} label="Password" type="password" variant="outlined" />
                        <Button variant="contained" color="secondary" onClick={this.login}>Log In</Button>
                        <div className="buttons">
                            <DelayLink to="/forgot" delay={500}><Button className="forgot" color="primary" onClick={()=>this.setPanel('forgot')}>Forgot Password?</Button></DelayLink>
                            <DelayLink to="/signup" delay={500}><Button className="signup" color="primary" onClick={()=>this.setPanel('signup')}>Sign Up</Button></DelayLink>
                        </div>
                    </div>
                    {this.state.success.length > 0 && <div className="success">{this.state.success}</div>}
                </Paper>
                </div>
                : this.state.panel === 'signup' ?
                <div className="Login Signup">
                    <Helmet>
                        <title>Sign Up - Thomas Yarnell Photography</title>
                    </Helmet>
                <h1 className="title">Sign Up</h1>
                <Paper className="container" elevation={5}>
                    {this.state.errors.length > 0 && <div className="errors">{this.state.errors}</div>}
                    <div className="input">
                        <div className="google">
                                <hr />
                                <Button className="googleLogin" onClick={this.logInWithGoogle}><p>Sign Up with</p><img src={googleLogo} alt="google logo"/></Button>
                                <hr />  
                        </div>
                        <div className="halfContainer">
                            <TextField error={!this.state.firstNameValid} value={this.state.firstName} onChange={this.updateFirstName} fontWeight={600} label="First Name" variant="outlined" />
                            <TextField error={!this.state.lastNameValid} value={this.state.lastName} onChange={this.updateLastName} fontWeight={600} label="Last Name" variant="outlined" />
                        </div>
                        <div className="halfContainer">
                            <TextField error={!this.state.phoneValid} value={this.state.phone} onChange={this.updatePhone} fontWeight={600} label="Phone Number" type="tel" variant="outlined"/>
                            <TextField error={!this.state.emailValid} value={this.state.email} onChange={this.updateEmail} fontWeight={600} label="Email" type="email" variant="outlined" />
                        </div>
                        <TextField error={!this.state.pwValid} value={this.state.password} onChange={this.updatePassword} fontWeight={600} label="Password" type="password" variant="outlined" autoComplete="new-password"/>
                        <TextField error={!this.state.confValid} value={this.state.confirmPassword} onChange={this.updateConfirmPassword} fontWeight={600} label="Confirm Password" type="password" variant="outlined" autoComplete="new-password"/>
                        <Button onClick={this.signUp} variant="contained" color="secondary">Sign Up</Button>
                        <div className="buttons">
                            <DelayLink to="/forgot" delay={500}><Button className="forgot" color="primary" onClick={()=>this.setPanel('forgot')}>Forgot Password?</Button></DelayLink>
                            <DelayLink to="/login" delay={500}><Button className="signup" color="primary" onClick={()=>this.setPanel('login')}>Log In</Button></DelayLink>
                        </div>
                        {this.state.success.length > 0 && <div className="success animate__animated animate_fadeIn">{this.state.success}</div>}
                    </div>
                </Paper>
                </div>
                :
                <div className="Login Forgot">
                    <Helmet>
                        <title>Forgot Password? - Thomas Yarnell Photography</title>
                    </Helmet>
                <h1 className="title">Forgot Password?</h1>
                <Paper className="container" elevation={5}>
                    {this.state.errors.length > 0 && <div className="errors">{this.state.errors}</div>}
                    <div className="input">
                        <TextField error={!this.state.emailValid} value={this.state.email} onChange={this.updateEmail} fontWeight={600} label="Email" variant="outlined" />
                        <Button onClick={this.forgot} variant="contained" color="secondary">Send Password Reset Email</Button>
                        <div className="buttons">
                            <DelayLink to="/signup" delay={500}><Button className="forgot" color="primary" onClick={()=>this.setPanel('signup')}>Sign Up</Button></DelayLink>
                            <DelayLink to="/login" delay={500}><Button className="signup" color="primary" onClick={()=>this.setPanel('login')}>Log In</Button></DelayLink>
                        </div>
                        {this.state.success.length > 0 && <div className="success">{this.state.success}</div>}
                    </div>
                </Paper>
                </div>
        )
    }
}

export default withRouter(Login);