import { React, Component } from 'react'

import { Paper } from '@material-ui/core';

import profile from '../media/self/profile.jpg';
import covid from '../media/self/covid.jpg';
import studio from '../media/self/studio.jpg';

import lens from '../media/self/gear/lens.jpg';
import camera from '../media/self/gear/camera.png';
import tripod from '../media/self/gear/tripod.jpg';

import facebook from '../media/socials/facebook.png';
import instagram from '../media/socials/instagram.png';
import pinterest from '../media/socials/pinterest.png';

import Helmet from 'react-helmet';

class About extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
            scrolled: false
        }

        this.scrollDown = this.scrollDown.bind(this)
    }

    scrollDown() {
        document.querySelector('.About').scroll({
            top: window.innerHeight,
            left: 0,
            behavior: 'smooth'
          });
        this.setState({scrolled: true})
    }

    componentDidMount() {
        let counter = 0;
        const scrollHandler = () => {
            if (counter > 1) {
                document.querySelector('.About').removeEventListener('mousewheel', scrollHandler, {passive: true})
                document.querySelector('.About').removeEventListener('DOMMouseScroll', scrollHandler, {passive: true})
                document.querySelector('.About').removeEventListener('onmousewheel', scrollHandler, {passive: true})
                document.querySelector('.About').removeEventListener('onwheel', scrollHandler, {passive: true})
                this.setState({scrolled: true})
            }
            counter++;
        }

        document.querySelector('.About').addEventListener('mousewheel', scrollHandler, {passive: true})
        document.querySelector('.About').addEventListener('DOMMouseScroll', scrollHandler, {passive: true})
        document.querySelector('.About').addEventListener('onmousewheel', scrollHandler, {passive: true})
        document.querySelector('.About').addEventListener('onwheel', scrollHandler, {passive: true})
    }

    render() {
        return (
            <div className="About">
                <Helmet>
                    <title>About - Thomas Yarnell Photography</title>
                </Helmet>
                <div className={`scroll animate__animated ${this.state.scrolled && 'hidden'}`} onClick={this.scrollDown}></div>
                <h1 className="title">About Thomas</h1>
                <div className="main">
                    <div className="columns">
                        <div className="column column1"> 
                            <Paper className="fullPaper">
                                <h3>About Me</h3>
                                <p>Hello! I am Thomas Yarnell and have my roots in Seattle, Washington. Im currently 23 years old and just out of college, chasing my love for photography (safely) in Los Angeles. Outside of my career, I enjoy spending time with friends, hiking, and traveling. One of my favorite places to visit is Big Bear Lake in the wintertime when snow covers the entire landscape.</p>
                                <h3>My Background</h3>
                                <p>Without a doubt, my biggest passion in life is photography. I started off as a hobbyist in high school when I had just turned 16. During the night of my birthday celebration, my father mentioned a present he had planned for me. When he brought it out, I excitedly tore away all the wrapping paper enclosing my present and underneath I soon uncovered a magnificent Nikon DSLR camera. In the beginning, I never understood all the buttons and functions of the camera, but I knew there were tons of lenses and photography techniques to try that would push my skills to the next level. I inevitably developed an unbreakable bond with my camera because the idea of cementing a meaningful moment within a picture inspired me not only to pursue photography as a hobby, but pursue it as an actual career in the future. </p>
                            </Paper>
                            <img src={studio} className="halfImage" alt="Studio"/>
                        </div>
                        <div className="column column2">
                            <div className="socials">
                                    <a href="https://www.facebook.com/thomas.yarnell.52" target="_blank" rel="noreferrer"><img src={facebook} alt="facebook"/></a>
                                    <a href="https://www.instagram.com/thomasyarnellphotography/" target="_blank" rel="noreferrer"><img src={instagram} alt="instagram"/></a>
                                    <a href="https://www.pinterest.com/thomasyarnellphotos/" target="_blank" rel="noreferrer"><img src={pinterest} alt="pinterest"/></a>
                            </div>
                            <img src={profile} className="fullImage" alt="photo of Thomas"/>
                            <Paper className="halfPaper">
                                <h3>The Founding of Thomas Yarnell Photography</h3>
                                <p>Once the coronavirus pandemic cleared and life was returned to a sense of normalcy, I was stuck on two pathways: a job in business or a job in photography. Working in business would have never given me the same satisfaction as working in photography. Passion for the arts and my intuition nudged me to start my own studio. In the beginning, the studio was really a small, one bedroom apartment in LA that barely fit in all my lighting and camera equipment, however, once I started getting bookings from local clients, my dreams of expanding the studio came closer into view. This transition signified the end of photography as my hobby and the beginning of my career  taking pictures of anything ranging from self-portraits to weddings and sports events. </p>
                            </Paper>
                        </div>
                        <div className="column">
                            <Paper className="fullPaper">
                                <h3>My Purpose</h3>
                                <p>The purpose of my work is to professionally capture and preserve moments that hold a priceless value to my clients. Just like any other medium of art such as painting or music, each photograph conveys its own unique message or story. By framing it within a picture, an individual can look back upon that moment as if they were actually there again, bringing them joy and happiness.</p>
                                <h3>Capturing COVID-19</h3>
                                <p>After high school, I continued schooling at the University of California Los Angeles where I majored in Business and Finance. I completed four years of undergraduate studies and before entering the job market, I decided to take a break to focus on photography. Unfortunately, the same year I made this decision, the world had begun to feel the effects of the coronavirus pandemic. Schools closed down, adults became unemployed, hospitals were overflowing with patients, and everyone was locked inside their homes. Although I could not stop the virus, I knew I could carry around my camera, document history, and capture the pandemics widespread impacts. Photographs revealed the depths of peoples' hardships to the world.</p>
                            </Paper>
                            <img src={covid} className="halfImage" alt="COVID motivation photo"/>
                        </div>
                    </div>
                    <Paper className="widePaper">
                        <h1>My Gear</h1>
                        <div className="items">
                            <div className="item">
                                <a href="https://www.bhphotovideo.com/c/product/1433714-REG/canon_rf_35mm_f_1_8_is.html" target="_blank" rel="noreferrer"><img src={lens} alt="lens" /></a>
                                <h3><a href="https://www.bhphotovideo.com/c/product/1433714-REG/canon_rf_35mm_f_1_8_is.html" target="_blank" rel="noreferrer">Canon RF 35mm f/1.8 IS <br></br>Macro STM Lens</a></h3>
                            </div>
                            <div className="item">
                                <a href="https://www.usa.canon.com/internet/portal/us/home/products/details/cameras/eos-dslr-and-mirrorless-cameras/mirrorless/eos-r5" target="_blank" rel="noreferrer"><img src={camera} alt="camera" /></a>
                                <h3><a href="https://www.usa.canon.com/internet/portal/us/home/products/details/cameras/eos-dslr-and-mirrorless-cameras/mirrorless/eos-r5" target="_blank" rel="noreferrer">Canon EOS R5</a></h3>
                            </div>
                            <div className="item">
                                <a href="https://www.peakdesign.com/products/travel-tripod" target="_blank" rel="noreferrer"><img src={tripod} alt="tripod" /></a>
                                <h3><a href="https://www.peakdesign.com/products/travel-tripod" target="_blank" rel="noreferrer">Peak Design Travel Tripod</a></h3>
                            </div>
                        </div>
                    </Paper>
                </div>
            </div>
        )
    }
}

export default About
