import React, { Component } from 'react'
import {Helmet} from 'react-helmet';
import {Link, withRouter} from "react-router-dom";

import { Button, Paper, TextField } from '@material-ui/core';

import facebook from '../media/socials/facebook.png';
import instagram from '../media/socials/instagram.png';
import pinterest from '../media/socials/pinterest.png';

const axios = require('axios');

const emailRegex = /^(([^<>()[\].,;:\s@"]+(.[^<>()[\].,;:\s@"]+)*)|(".+"))@(([^<>()[\].,;:\s@"]+\.)+[^<>()[\].,;:\s@"]{2,})$/i;
const nameRegex = /^[ a-zA-Z\-]+$/;
const phoneRegex = /^[\d\-() ]{0,25}$/;
const phoneFinalRegex = /^[0-9]{10,11}$/;

export class Contact extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
            panel: this.props.panel,
            
            firstName: '',
            lastName: '',
            email: '',
            phone: '',
            subject: '',
            message: '',


            firstNameValid: true,
            lastNameValid: true,
            phoneValid: true,
            emailValid: true,
            subjectValid: true,
            messageValid: true,
            errors: [],
            success: ''
        }

        this.updateEmail = this.updateEmail.bind(this);
        this.updateSubject = this.updateSubject.bind(this);
        this.updateMessage = this.updateMessage.bind(this);
        this.updateFirstName = this.updateFirstName.bind(this);
        this.updateLastName = this.updateLastName.bind(this);
        this.updatePhone = this.updatePhone.bind(this);

        this.sendMessage = this.sendMessage.bind(this);
    }

    componentDidMount() {
        axios.get('/user').then(res => {
            if (res.status === 200) {
                this.setState({firstName: res.data.first_name, lastName: res.data.last_name, email: res.data.email, phone: res.data.phone_num})
            }
        }).catch(err => {})
    }

    updateEmail(event) {
        this.setState({email: event.target.value, emailValid: true});
    }

    updateSubject(event) {
        this.setState({subject: event.target.value, subjectValid: true});
    }
    
    updateMessage(event) {
        this.setState({message: event.target.value,  messageValid: true});
    }

    updateFirstName(event) {
       (event.target.value === '' || event.target.value.length === 0 || nameRegex.test(event.target.value)) && this.setState({firstName: event.target.value, firstNameValid: true});
    }

    updateLastName(event) {
        (event.target.value === '' || event.target.value.length === 0 || nameRegex.test(event.target.value)) && this.setState({lastName: event.target.value, lastNameValid: true});
    }

    updatePhone(event) {
        const value = event.target.value;
        if((phoneRegex.test(value) && value.split(" ").length - 1 <= 4) || value.length === 0) {
            this.setState({phone: value, phoneValid: true});  
        }
    }

    sendMessage() {
        let firstNameValid = false;
        let lastNameValid = false;
        let phoneValid = false;
        let emailValid = false;
        let subjectValid = false;
        let messageValid = false;
        let errors = [];

        if (this.state.firstName.length > 1) {
            firstNameValid = true;
        } else {
            errors.push(<p>Please enter a first name.</p>);
        }

        if (this.state.lastName.length > 1) {
            lastNameValid = true;
        } else {
            errors.push(<p>Please enter a last name.</p>);
        }

        if (emailRegex.test(this.state.email)) {
            emailValid = true;
        } else {
            errors.push(<p>Please enter a valid email.</p>);
        }

        if (phoneRegex.test(this.state.phone) && this.state.phone.split(" ").length - 1 <= 4 && this.state.phone.length > 0 && phoneFinalRegex.test(this.state.phone.replace(/-/g, "").replace(/[()]/g, "").replace(/\s/g, ""))) {
            phoneValid = true;  
        } else {
            errors.push(<p>Please enter a valid phone number.</p>);
        }

        if (this.state.subject.length > 0) {
            subjectValid = true;
        } else {
            errors.push(<p>Please enter a subject.</p>);
        }

        if (this.state.message.length > 0) {
            messageValid = true;
        } else {
            errors.push(<p>Please enter a message.</p>);
        }

        if (firstNameValid && lastNameValid && emailValid && phoneValid && subjectValid && messageValid) {
            axios.post('/contact', {
                first_name: this.state.firstName,
                last_name: this.state.lastName,
                email: this.state.email,
                phone: this.state.phone,
                subject: this.state.subject,
                message: this.state.message
            }).then(res => {
                if (res.status === 200) {
                    this.setState({errors: [], success: 'Message sent! Check your email for a copy of the message.', subject: '', message: '', emailValid: true})
                } else {
                    this.setState({errors: [<p>Error sending message. Please try again later.</p>], success: ''})
                }
            }).catch(err => {
                this.setState({errors: [<p>Error sending message. Please try again later.</p>], success: ''})
            })
        } else {
            this.setState({firstNameValid, lastNameValid, phoneValid, subjectValid, emailValid, messageValid, errors, success: ''})
        }
    }

    render() {
        return (
                <div className="Login Book Contact">
                    <Helmet>
                        <title>Contact - Thomas Yarnell Photography</title>
                    </Helmet>
                    <h1 className="title">Contact Me</h1>
                    <div className="socials">
                        <a href="https://www.facebook.com/thomas.yarnell.52" target="_blank" rel="noreferrer"><img src={facebook} alt="facebook"/></a>
                        <a href="https://www.instagram.com/thomasyarnellphotography/" target="_blank" rel="noreferrer"><img src={instagram} alt="instagram"/></a>
                        <a href="https://www.pinterest.com/thomasyarnellphotos/" target="_blank" rel="noreferrer"><img src={pinterest} alt="pinterest"/></a>
                    </div>
                    <Paper className="container" elevation={5}>
                        {this.state.errors.length > 0 && <div className="errors">{this.state.errors}</div>}
                        <div className="input">
                                <TextField className="name" error={!this.state.firstNameValid} value={this.state.firstName} onChange={this.updateFirstName} fontWeight={600} label="First Name" variant="outlined" />
                                <TextField className="name" error={!this.state.lastNameValid} value={this.state.lastName} onChange={this.updateLastName} fontWeight={600} label="Last Name" variant="outlined" />
                                <TextField className="phone" error={!this.state.phoneValid} value={this.state.phone} onChange={this.updatePhone} fontWeight={600} label="Phone Number" type="tel" variant="outlined"/>
                                <TextField className="subject" error={!this.state.subjectValid} value={this.state.subject} onChange={this.updateSubject} fontWeight={600} label="Subject" type="tel" variant="outlined"/>
                                <TextField className="email" error={!this.state.emailValid} value={this.state.email} onChange={this.updateEmail} fontWeight={600} label="Email" type="email" variant="outlined" />
                                <TextField
                                    className="notes"
                                    label="Message"
                                    multiline
                                    rows={5}
                                    variant="outlined"
                                    value={this.state.message}
                                    onChange={this.updateMessage}
                                    error={!this.state.messageValid}
                                />
                        </div>
                        <div className="BookButtons">
                            <Button onClick={this.sendMessage} variant="contained" color="secondary">&nbsp;&nbsp;Send&nbsp;&nbsp;</Button>
                        </div>
                        {this.state.success.length > 0 && <div className="success animate__animated animate_fadeIn">{this.state.success}</div>}
                    </Paper>
                </div>
        )
    }
}

export default withRouter(Contact);