import React, { Component } from 'react'
import Helmet from 'react-helmet';

import { Button, TextField, FormGroup, FormControlLabel, Checkbox, IconButton, DialogTitle, DialogActions, Snackbar, InputAdornment } from '@material-ui/core';
import DelayLink from './DelayLink';
import logo from '../media/mainlogo.png';

import AddShoppingCartIcon from '@material-ui/icons/AddShoppingCart';
import AddIcon from '@material-ui/icons/Add';
import RemoveIcon from '@material-ui/icons/Remove';
import SearchIcon from '@material-ui/icons/Search';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import ArrowUpwardIcon from '@material-ui/icons/ArrowUpward';
import ArrowDownwardIcon from '@material-ui/icons/ArrowDownward';

import Fuse from 'fuse.js';

const axios = require('axios');

const formatter = new Intl.NumberFormat('en-US', {
    style: 'currency',
    currency: 'USD',
});

let originalImages = [];

export class Shop extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
            scrollToTop: false,
            throttleScroll: false,
            cart: [],
            search: '',
            fuse: new Fuse(Object.values(this.props.captions).map(d => d[0]), {threshold: 0.4}),
            open: false,
            addItem: [],
            selections: [],
            selected: 0,
            amount: 1,
            notification: '',
            totalPrice: 0,

            filters: [['Weddings', false], ['Sports Events', false], ['Modeling', false], ['Landscape', false], ['Graduations', false], ['Family Portraits', false], ['Covid-19', false]],
            formattedImages: new Array(Object.keys(this.props.captions).length),
            imagesShown: new Array(Object.keys(this.props.captions).length).fill(true),
            formattedCategories: []
        }

        this.updateSearch = this.updateSearch.bind(this);
        this.showNotLogged = this.showNotLogged.bind(this);
        this.showQuantity = this.showQuantity.bind(this);
        this.addToCart = this.addToCart.bind(this);
        this.scrollHandler = this.scrollHandler.bind(this);
        this.scrollAction = this.scrollAction.bind(this);
    }

    scrollHandler() {
        if (!this.state.throttleScroll) {
            if (document.querySelector('.main').scrollTop > 200) {
                this.setState({scrollToTop: true, throttleScroll: true})
                setTimeout(() => this.setState({throttleScroll: false}), 250)
            } else {
                this.setState({scrollToTop: false, throttleScroll: true})
                setTimeout(() => this.setState({throttleScroll: false}), 250)
            }
        }
    }

    scrollAction() {
        if (this.state.scrollToTop) {
            document.querySelector('.main').scroll({
                top: 0,
                left: 0,
                behavior: 'smooth'
                });
            this.setState({scrollToTop: false})
        }
    }

    componentDidMount() {
        document.querySelector('.main').addEventListener('mousewheel', this.scrollHandler, {passive: true})
        document.querySelector('.main').addEventListener('DOMMouseScroll', this.scrollHandler, {passive: true})
        document.querySelector('.main').addEventListener('onmousewheel', this.scrollHandler, {passive: true})
        document.querySelector('.main').addEventListener('onwheel', this.scrollHandler, {passive: true})

        axios.get('/user').then(res => {
            if (res.status === 200 || res.status === 304) {
                let totalPrice = 0;
                res.data.cart.map(item => {totalPrice += item.price * item.quantity})
                this.setState({cart: res.data.cart, totalPrice})
            }
        }).catch(err => {})
        let formattedImages = new Array(Object.keys(this.props.captions).length);
        let results = [];
        if (this.state.search !== '') {
            results = this.state.fuse.search(this.state.search);
        }

        let imageCounter = 0;

        const imagesRendered = () => {
            return new Promise((resolve, reject) => {
                (function waitForImages() {
                    if (imageCounter === 30) {
                        return resolve();
                    }
                    setTimeout(waitForImages, 30);
                })()
            })
        }

        let noFilters = false;
        if (!this.state.filters.map(filter => filter[1]).includes(true)) {
            noFilters = true;
        }

        for (let i = 0; i < Object.keys(this.props.captions).length; i++) {
            const image = Object.keys(this.props.captions)[i];
            if ((noFilters || this.state.filters[this.props.captions[image][1]][1]) && (results.map(result => result.item).includes(this.props.captions[image][0]) || this.state.search === '' || this.state.search === undefined)) {
                let img = new Image();
                img.src = image;
                img.onload = () => {
                    formattedImages[i] = 
                        <div key={image} style={{display: !this.state.imagesShown[i] && 'none'}} className={`shopLazyLoad ${img.naturalWidth < img.naturalHeight ? 'portrait' : 'landscape'}`}>
                            <img src={image}/>
                            <div className="priceOverlay" onClick={() => this.props.loggedIn ? this.showQuantity(image, img.naturalWidth < img.naturalHeight ? 'portrait' : 'landscape') : this.showNotLogged()}>
                                <p>{formatter.format(this.props.captions[image][2])}</p>
                            </div>
                            <div className="titleAdd">
                                <p>{this.props.captions[image][0] || 'Photo Print'}</p>
                                <IconButton onClick={() => this.props.loggedIn ? this.showQuantity(image, img.naturalWidth < img.naturalHeight ? 'portrait' : 'landscape') : this.showNotLogged()}><AddShoppingCartIcon /></IconButton>
                            </div>
                        </div>;
                    imageCounter++;
                }
            }
        }

        originalImages = formattedImages;

        imagesRendered().then(() => {
            this.setState({
                formattedImages,
                formattedCategories: 
                this.state.filters.map((category, index) => {
                    return (<FormControlLabel key={category[0]}
                    control={
                    <Checkbox
                        checked={this.state.filters[index][1]}
                        onChange={() => this.updateFilters(index)}
                        name={category[0]}
                        color="primary"
                    />
                    }
                    label={category[0]}
                />);
                })
            })
        })
    }

    updateFilters(index) {
        let filters = [...this.state.filters];
        filters[index][1] = !filters[index][1];
        this.setState({filters}, () => this.reRender());
    }

    updateSearch(event) {
        this.setState({search: event.target.value}, () => this.reRender());
    }

    showNotLogged() {
        this.setState({open: true})
    }

    showQuantity(image, orientation) {
        this.setState({addItem: image, selections: orientation === 'portrait' ? ['12" x 18"', '16" x 24"', '24" x 36"', '32" x 48"'] : ['18" x 12"', '24" x 16"', '36" x 24"', '48" x 32"']})
    }

    changeAmount(diff) {
        if (this.state.amount + diff > 0 && ((!(this.state.cart.some(el => el.url === this.state.addItem && el.size === this.state.selected)) && this.state.amount + diff <= 10) || (this.state.cart.some(el => el.url === this.state.addItem && el.quantity + this.state.amount + diff <= 10)))) {
            this.setState({amount: this.state.amount + diff})
        }

        if ((this.state.cart.some(el => el.url === this.state.addItem && el.size === this.state.selected && el.quantity + this.state.amount + diff > 10)) || this.state.amount + diff > 10) {
            this.setState({notification: 'Maximum of 10 per print reached.'})
            setTimeout(() => this.setState({notification: ''}), 1500);
        }
    }

    changeSelection(diff) {
        this.setState({selected: this.state.selected+diff > 3 ? 3 : this.state.selected+diff < 0 ? 0 : (this.state.selected + diff)})
    }

    addToCart() {
        axios.post('/shop', {
            id: this.state.addItem,
            quantity: this.state.amount,
            price: (this.state.selected === 0 ? this.props.captions[this.state.addItem][2] : this.state.selected === 1 ? this.props.captions[this.state.addItem][2] * 1.2 : this.state.selected === 2 ? this.props.captions[this.state.addItem][2] * 1.4 : this.props.captions[this.state.addItem][2] * 2),
            size: this.state.selected,
        }).then(res => {
            if (res.status === 200 || res.status === 304) {
                axios.get('/user').then(res => {
                    let totalPrice = 0;
                    res.data.cart.map(item => {totalPrice += item.price * item.quantity})
                    this.setState({cart: res.data.cart, totalPrice})
                })
                this.setState({notification: 'Succesfully added item to cart.', addItem: [], selected: 0})
                setTimeout(() => this.setState({amount: 1}), 600)
                setTimeout(() => this.setState({notification: ''}), 4000)
            }
        }).catch(err => {});
    }

    componentWillUnmount() {
        document.querySelector('.main').removeEventListener('mousewheel', this.scrollHandler, {passive: true})
        document.querySelector('.main').removeEventListener('DOMMouseScroll', this.scrollHandler, {passive: true})
        document.querySelector('.main').removeEventListener('onmousewheel', this.scrollHandler, {passive: true})
        document.querySelector('.main').removeEventListener('onwheel', this.scrollHandler, {passive: true})
    }

    reRender() {
        let imagesShown = [];
        let results = [];
        if (this.state.search !== '') {
            results = this.state.fuse.search(this.state.search);
        } else {
            results = Object.values(this.props.captions).map(d => {return {item: d[0]}});
        }

        let noFilters = false;
        if (!this.state.filters.map(filter => filter[1]).includes(true)) {
            noFilters = true;
        }

        for (let i = 0; i < Object.keys(this.props.captions).length; i++) {
            const image = Object.keys(this.props.captions)[i];
            if ((noFilters || this.state.filters[this.props.captions[image][1]][1]) && (results.map(result => result.item).includes(this.props.captions[image][0]) || this.state.search === '' || this.state.search === undefined)) {
                imagesShown.push(true);
            } else {
                imagesShown.push(false);
            }
        }

        this.setState({imagesShown: imagesShown, 
            formattedImages: originalImages.filter((element, index) => {
                if (imagesShown[index]) {
                    return true;
                } else {
                    return false;
                }
            }),
            formattedCategories: 
            this.state.filters.map((category, index) => {
                return (<FormControlLabel key={category[0]}
                control={
                <Checkbox
                    checked={this.state.filters[index][1]}
                    onChange={() => this.updateFilters(index)}
                    name={category[0]}
                    color="primary"
                />
                }
                label={category[0]}
            />);
            })
        });
    }

    render() {
        return (
            <div className="Shop">
                <Helmet>
                    <title>Shop - Thomas Yarnell Photography</title>
                </Helmet>
                <IconButton onClick={this.scrollAction} className={`scrollToTop animate__animated ${this.state.scrollToTop ? 'animate__fadeIn' : 'animate__fadeOut'}`}><ArrowUpwardIcon fontSize="large"/></IconButton>
                <div className="main">
                    {this.props.loggedIn ?
                    <DelayLink to="/dashboard/cart" delay={600} onDelayStart={() => {this.props.runLoader({toggle: false, time: 750})}}><Button variant="contained" className="toCart" color="secondary">({formatter.format(this.state.totalPrice)})&nbsp;<ShoppingCartIcon/></Button></DelayLink>
                    :
                    <Button variant="contained" className="toCart" color="secondary" onClick={() => this.setState({open: true})}>({formatter.format(this.state.totalPrice)})&nbsp;<ShoppingCartIcon/></Button>
                    }
                    <h1 className="title">Shop</h1>
                    <div className="search">
                        <TextField variant="outlined" label="Search Shop" color="primary" value={this.state.search} onChange={this.updateSearch} InputProps={{endAdornment: <InputAdornment position="end"><SearchIcon color="primary"/></InputAdornment>}}/>
                        <FormGroup className="filters">
                            {this.state.formattedCategories}
                        </FormGroup>
                    </div>
                    <div className="formattedImages">
                        {this.state.formattedImages}
                    </div>
                </div>
                <div className={`shopPopUp ${this.state.open ? 'visible' : 'hidden'}`} onClick={() => this.setState({open: false})}>
                    <div className="popContainer">
                        <img src={logo} />
                        <DialogTitle>Sign Up To . . .</DialogTitle>
                        <p>Keep Track of Your Bookings</p>
                        <p>Save Your Favorites</p>
                        <p>Checkout Items</p>
                        <DialogActions className="buttons">
                            <DelayLink to="/signup" delay={600} onDelayStart={() => {this.props.runLoader({toggle: false, time: 750})}}><Button variant="contained" color="secondary">Sign Up</Button></DelayLink>
                            <DelayLink to="/login" delay={600} onDelayStart={() => {this.props.runLoader({toggle: false, time: 750})}}><Button onClick={this.toLogIn} variant="contained" color="primary">Log In</Button></DelayLink>
                        </DialogActions>
                    </div>
                </div>
                <div className={`addToCart shopPopUp ${this.state.addItem.length > 0 ? 'visible' : 'hidden'}`}>
                    <div className="popContainer">
                        <DialogTitle>Add Item to Cart?</DialogTitle>
                        <p>Quantity: <IconButton onClick={() => this.changeAmount(-1)}><RemoveIcon/></IconButton> {this.state.amount} <IconButton onClick={() => this.changeAmount(1)}><AddIcon/></IconButton></p>
                        <p>Size:<IconButton onClick={() => this.changeSelection(-1)}><ArrowDownwardIcon/></IconButton> {this.state.selections[this.state.selected]} <IconButton onClick={() => this.changeSelection(1)}><ArrowUpwardIcon/></IconButton></p>
                        <p>Price: ${this.state.addItem.length > 0 && (this.state.selected === 0 ? this.props.captions[this.state.addItem][2] : this.state.selected === 1 ? this.props.captions[this.state.addItem][2] * 1.2 : this.state.selected === 2 ? this.props.captions[this.state.addItem][2] * 1.4 : this.props.captions[this.state.addItem][2] * 2)}.00</p>
                        <DialogActions className="buttons">
                            <Button variant="contained" color="secondary" onClick={() => this.setState({addItem: [], selections: [], selected: 0, amount: 1})}>Cancel</Button>
                            <Button variant="contained" color="primary" onClick={this.addToCart}>Add To Cart</Button>
                        </DialogActions>
                    </div>
                </div>
                <Snackbar
                    open={this.state.notification.length > 0}
                    anchorOrigin={{vertical: 'top', horizontal: 'center'}}
                    message={this.state.notification}
                    autoHideDuration={4000}
                />
            </div>
        )
    }
}

export default Shop
