import React, { Component } from 'react'
import { Button, Snackbar, Dialog, DialogTitle, DialogActions, TextField } from '@material-ui/core';

const axios = require('axios');

const emailRegex = /^(([^<>()[\].,;:\s@"]+(.[^<>()[\].,;:\s@"]+)*)|(".+"))@(([^<>()[\].,;:\s@"]+\.)+[^<>()[\].,;:\s@"]{2,})$/i;
const pwRegex = /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{8,}$/;
const nameRegex = /^[ a-zA-Z\-]+$/;
const phoneRegex = /^[\d\-() ]{0,25}$/;
const phoneFinalRegex = /^[0-9]{10,11}$/;

export class User extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
            googleOnlyUser: true,
            firstName: '',
            lastName: '',
            email: '',
            newPassword: '',
            confirmPassword: '',
            oldPassword: '',
            phone: '',
            changed: false,
            pastEmail: '',

            firstNameValid: true,
            lastNameValid: true,
            phoneValid: true,
            emailValid: true,
            pwValid: true,
            confValid: true,
            oldValid: true,
            errors: [],
            success: ''
        }

        this.updateEmail = this.updateEmail.bind(this);
        this.updatePassword = this.updatePassword.bind(this);
        this.updateConfirmPassword = this.updateConfirmPassword.bind(this);
        this.updateOldPassword = this.updateOldPassword.bind(this);
        this.updateFirstName = this.updateFirstName.bind(this);
        this.updateLastName = this.updateLastName.bind(this);
        this.updatePhone = this.updatePhone.bind(this);
        this.save = this.save.bind(this);
    }
    
    componentDidMount() {
        axios.get('/user').then(res => {
            this.setState({
                firstName: res.data.first_name,
                lastName: res.data.last_name,
                email: res.data.email,
                phone: res.data.phone_num !== undefined ? res.data.phone_num + '' : '',
                pastEmail: res.data.email,
                googleOnlyUser: (res.data.password === null || res.data.password === undefined) && res.data.googleId
            })
        }).catch(err => {
        })
    }
    
    updateEmail(event) {
        this.setState({email: event.target.value, emailValid: true, changed: true});
    }

    updatePassword(event) {
        this.setState({newPassword: event.target.value, pwValid: true});
    }
    
    updateConfirmPassword(event) {
        this.setState({confirmPassword: event.target.value,  confValid: true, changed: true});
    }

    updateOldPassword(event) {
        this.setState({oldPassword: event.target.value,  oldValid: true});
    }

    updateFirstName(event) {
       (event.target.value === '' || event.target.value.length === 0 || nameRegex.test(event.target.value)) && this.setState({firstName: event.target.value, firstNameValid: true, changed: true});
    }

    updateLastName(event) {
        (event.target.value === '' || event.target.value.length === 0 || nameRegex.test(event.target.value)) && this.setState({lastName: event.target.value, lastNameValid: true, changed: true});
    }

    updatePhone(event) {
        const value = event.target.value;
        if ((phoneRegex.test(value) && value.split(" ").length - 1 <= 4) || value.length === 0) {
            this.setState({phone: value, phoneValid: true, changed: true});  
        }
    }

    save() {
        if (this.state.googleOnlyUser) {
            if (this.state.changed) {
                let firstNameValid = false;
                let lastNameValid = false;
                let phoneValid = false;
                let emailValid = false;
                let errors = [];
    
                if (this.state.firstName.length > 1) {
                    firstNameValid = true;
                } else {
                    errors.push(<p>Please enter a first name.</p>);
                }
    
                if (this.state.lastName.length > 1) {
                    lastNameValid = true;
                } else {
                    errors.push(<p>Please enter a last name.</p>);
                }
    
                if (this.state.phone.length === 0 || this.state.phone === undefined || (phoneRegex.test(this.state.phone) && this.state.phone.split(" ").length - 1 <= 4 && phoneFinalRegex.test(this.state.phone.replace(/-/g, "").replace(/[()]/g, "").replace(/\s/g, "")) && (this.state.phone.length > 0 || typeof this.state.phone === 'number'))) {
                    phoneValid = true;  
                } else {
                    errors.push(<p>Please enter a valid phone number.</p>);
                }
    
                if (emailRegex.test(this.state.email)) {
                    emailValid = true;
                } else {
                    errors.push(<p>Please enter a valid email.</p>);
                }
    
                let data = {
                    first_name: this.state.firstName,
                    last_name: this.state.lastName, 
                    email: this.state.email,
                    old_email: this.state.pastEmail,
                    phone_num: this.state.phone
                }
                
                // if (typeof this.state.phone === 'number') {
                //     data['phone_num'] = this.state.phone
                // } else {
                //     data['phone_num'] = ''
                // }
    
                if (emailValid && phoneValid && firstNameValid && lastNameValid) {
                    axios.patch('/user', data).then(res => {
                        if (res.status === 200) {
                            this.setState({success: this.state.email === this.state.pastEmail ? 'Successfully updated information. Please confirm any current bookings\' information is correct as well - it is NOT updated along this.' : 'Successfully updated information. Please check your email for a confirmation email. Please confirm any current bookings\' information is correct as well - it is NOT updated along this.', errors: [], firstNameValid: true,
                            lastNameValid: true,
                            phoneValid: true,
                            emailValid: true,
                            changed: false,
                            })
                        } else {
                            this.setState({errors: [<p>Error saving information. Please try again at another time.</p>], success: '', firstNameValid: true,
                            lastNameValid: true,
                            phoneValid: true,
                            emailValid: true})
                        }
                    }).catch(err => {
                        if (err.response.status === 401) {
                            errors = [<p>Incorrect Old Password.</p>]
                            this.setState({errors, success: '', firstNameValid: true,
                            lastNameValid: true,
                            phoneValid: true,
                            emailValid: true})
                        } else if (err.response.status === 408) {
                            errors = [<p>Email is already in use.</p>]
                            this.setState({errors, success: '', firstNameValid: true,
                            lastNameValid: true,
                            phoneValid: true,
                            emailValid: false})
                        } else {
                            errors = [<p>Error saving information. Please try again at another time.</p>]
                            this.setState({errors, success: '', firstNameValid: true,
                            lastNameValid: true,
                            phoneValid: true,
                            emailValid: true,})
                        }
                    })
                } else {
                    this.setState({emailValid: emailValid, firstNameValid: firstNameValid, lastNameValid: lastNameValid, phoneValid: phoneValid, errors: errors, success: ''})
                }
            }
        } else {
            if (this.state.changed) {
                let firstNameValid = false;
                let lastNameValid = false;
                let phoneValid = false;
                let emailValid = false;
                let pwValid = false;
                let confValid = false;
                let oldValid = false;
                let errors = [];
    
                if (this.state.firstName.length > 1) {
                    firstNameValid = true;
                } else {
                    errors.push(<p>Please enter a first name.</p>);
                }
    
                if (this.state.lastName.length > 1) {
                    lastNameValid = true;
                } else {
                    errors.push(<p>Please enter a last name.</p>);
                }
    
                if (phoneFinalRegex.test(this.state.phone.replace(/-/g, "").replace(/[()]/g, "").replace(/\s/g, "")) && phoneRegex.test(this.state.phone) && this.state.phone.split(" ").length - 1 <= 4 && (this.state.phone.length > 0 || typeof this.state.phone === 'number')) {
                    phoneValid = true;  
                } else {
                    errors.push(<p>Please enter a valid phone number.</p>);
                }
    
                if (emailRegex.test(this.state.email)) {
                    emailValid = true;
                } else {
                    errors.push(<p>Please enter a valid email.</p>);
                }
    
                if (pwRegex.test(this.state.newPassword) || this.state.newPassword === '' || this.state.newPassword === null) {
                    pwValid = true;
                } else {
                    errors.push(<p>Password does not meet complexity requirements.</p>);
                }
    
                if (this.state.newPassword === this.state.confirmPassword) {
                    confValid = true;
                } else {
                    errors.push(<p>Passwords do not match.</p>);
                }
    
                if (pwRegex.test(this.state.oldPassword)) {
                    oldValid = true;
                } else {
                    errors.push(<p>Incorrect Old Password.</p>);
                }
    
                let data = {
                    old_password: btoa(this.state.oldPassword),
                    first_name: this.state.firstName,
                    last_name: this.state.lastName, 
                    phone_num: this.state.phone,
                    email: this.state.email,
                    old_email: this.state.pastEmail
                }
    
                if (this.state.newPassword !== null && this.state.newPassword !== '' && pwRegex.test(this.state.newPassword)) {
                    data['new_password'] = btoa(this.state.newPassword);
                }
    
                if (emailValid && pwValid && confValid && firstNameValid && lastNameValid && phoneValid && oldValid) {
                    axios.patch('/user', data).then(res => {
                        if (res.status === 200) {
                            this.setState({success: this.state.email === this.state.pastEmail ? 'Successfully updated information. Please confirm any current bookings\' information is correct as well - it is NOT updated along this.' : 'Successfully updated information. Please check your email for a confirmation email. Please confirm any current bookings\' information is correct as well - it is NOT updated along this.', errors: [], firstNameValid: true,
                            lastNameValid: true,
                            phoneValid: true,
                            emailValid: true,
                            oldValid: true,
                            pwValid: true,
                            confValid: true,
                            confirmPassword: '',
                            oldPassword: '',
                            newPassword: '',
                            changed: false,
                            })
                            window.location.reload();
                        } else {
                            this.setState({errors: [<p>Error saving information. Please try again at another time.</p>], success: '', firstNameValid: true,
                            lastNameValid: true,
                            phoneValid: true,
                            emailValid: true,
                            pwValid: true,
                            confValid: true,
                            oldValid: true})
                        }
                    }).catch(err => {
                        if (err.response.status === 401) {
                            errors = [<p>Incorrect Old Password.</p>]
                            this.setState({errors, success: '', firstNameValid: true,
                            lastNameValid: true,
                            phoneValid: true,
                            emailValid: true,
                            pwValid: true,
                            confValid: true,
                            oldValid: false})
                        } else if (err.response.status === 408) {
                            errors = [<p>Email is already in use.</p>]
                            this.setState({errors, success: '', firstNameValid: true,
                            lastNameValid: true,
                            phoneValid: true,
                            emailValid: false,
                            pwValid: true,
                            confValid: true,
                            oldValid: true})
                        } else {
                            errors = [<p>Error saving information. Please try again at another time.</p>]
                            this.setState({errors, success: '', firstNameValid: true,
                            lastNameValid: true,
                            phoneValid: true,
                            emailValid: true,
                            pwValid: true,
                            confValid: true,
                            oldValid: true})
                        }
                    })
                } else {
                    this.setState({emailValid: emailValid, pwValid: pwValid, confValid: confValid, firstNameValid: firstNameValid, lastNameValid: lastNameValid, phoneValid: phoneValid, oldValid: oldValid, errors: errors, success: ''})
                }
            }
        }
    }

    render() {
        return (
            this.state.googleOnlyUser ?
            <div className="User">
                <div className="info googleOnly">
                    <div className="basic">
                        <TextField error={!this.state.firstNameValid} value={this.state.firstName} onChange={this.updateFirstName} fontWeight={600} label="First Name" variant="outlined" />
                        <TextField error={!this.state.lastNameValid} value={this.state.lastName} onChange={this.updateLastName} fontWeight={600} label="Last Name" variant="outlined" />
                    </div>
                    <hr />
                    <div className="confidential">
                        <TextField error={!this.state.emailValid} value={this.state.email} onChange={this.updateEmail} fontWeight={600} label="Email" type="email" variant="outlined" />
                        <TextField error={!this.state.phoneValid} value={this.state.phone} onChange={this.updatePhone} fontWeight={600} label="Phone Number" type="tel" variant="outlined"/>
                    </div>
                </div>
                <Button variant="contained" color="primary" onClick={this.save}>Save</Button>
                {this.state.errors.length > 0 && <div className="errors">{this.state.errors}</div>}
                {this.state.success.length > 0 && <div className="success">{this.state.success}</div>}
            </div>       
            :
            <div className="User">
                <div className="info">
                    <div className="basic">
                        <TextField error={!this.state.firstNameValid} value={this.state.firstName} onChange={this.updateFirstName} fontWeight={600} label="First Name" variant="outlined" />
                        <TextField error={!this.state.lastNameValid} value={this.state.lastName} onChange={this.updateLastName} fontWeight={600} label="Last Name" variant="outlined" />
                        <TextField error={!this.state.phoneValid} value={this.state.phone} onChange={this.updatePhone} fontWeight={600} label="Phone Number" type="tel" variant="outlined"/>
                    </div>
                    <hr />
                    <div className="confidential">
                        <TextField error={!this.state.emailValid} value={this.state.email} onChange={this.updateEmail} fontWeight={600} label="Email" type="email" variant="outlined" />
                        <TextField id="field1" error={!this.state.pwValid} value={this.state.newPassword} onChange={this.updatePassword} fontWeight={600} label="New Password" type="password" variant="outlined" autoComplete="new-password"/>
                        <TextField id="field2" error={!this.state.confValid} value={this.state.confirmPassword} onChange={this.updateConfirmPassword} fontWeight={600} label="Confirm Password" type="password" autoComplete="new-password" variant="outlined" />
                    </div>
                </div>
                <TextField id="field3" error={!this.state.oldValid} value={this.state.oldPassword} onChange={this.updateOldPassword} fontWeight={600} label="Re-enter Old Password" type="password" variant="outlined" autoComplete="new-password"/>
                <Button variant="contained" color="primary" onClick={this.save}>Save</Button>
                {this.state.errors.length > 0 && <div className="errors">{this.state.errors}</div>}
                {this.state.success.length > 0 && <div className="success">{this.state.success}</div>}
            </div>    
        )
    }
}

export default User
