import React, { Component } from 'react'
import { Helmet } from 'react-helmet';
import DelayLink from './DelayLink';

import landingImage from '../media/self/landing.webp';

export class Landing extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
            panel: undefined,
            panelAnim: undefined,
            nonPause: false,
        }

        this.scrollHandler = this.scrollHandler.bind(this)
    }
    
    componentDidMount() {
            this.setState({panel: undefined, panelAnim: undefined})
            let counter = 0;

            document.querySelector('.Landing').addEventListener('mousewheel', this.scrollHandler, {passive: true})
            document.querySelector('.Landing').addEventListener('DOMMouseScroll', this.scrollHandler, {passive: true})
            document.querySelector('.Landing').addEventListener('onmousewheel', this.scrollHandler, {passive: true})
            document.querySelector('.Landing').addEventListener('onwheel', this.scrollHandler, {passive: true})
    }

    componentWillUnmount() {
        document.querySelector('.Landing').removeEventListener('mousewheel', this.scrollHandler, {passive: true})
        document.querySelector('.Landing').removeEventListener('DOMMouseScroll', this.scrollHandler, {passive: true})
        document.querySelector('.Landing').removeEventListener('onmousewheel', this.scrollHandler, {passive: true})
        document.querySelector('.Landing').removeEventListener('onwheel', this.scrollHandler, {passive: true})
        this.setState({panel: undefined, panelAnim: undefined})
    }

    scrollHandler() {
        if (!this.state.panelAnim) {
            this.setState({panel: !this.state.panel, panelAnim: true})
            setTimeout(() => this.setState({panelAnim: false}), 1500)
        }
    }

    render() {
        return (
            <div className="Landing">
                <Helmet>
                    <title>Thomas Yarnell Photography</title>
                </Helmet>
                <img className={`landing-image animate__animated ${this.state.panel !== undefined && (this.state.panel ? 'animate__slideOutUp' : 'animate__slideInDown waitForOther')}`} src={landingImage} alt="self-portrait background"/>
                <div className={`scroll animate__animated ${this.state.panel !== undefined && (this.state.panel ? 'animate__slideOutDown' : 'animate__slideInUp')}`} onClick={this.scrollHandler}></div>
                <div className={`panel animate__animated ${this.state.panel !== undefined ? (this.state.panel ? 'animate__slideInUp waitForOther' : 'animate__slideOutDown') : 'no-pointer-events'}`}>
                    <DelayLink to="/portfolio" delay={600} onDelayStart={() => this.props.runLoader({time: 750, toggle: false})}><div className="select center">
                        <h1>Portfolio</h1>
                    </div></DelayLink>
                    <div style={{cursor: 'pointer'}} onClick={this.props.toggleNav} className="select tr">
                        <h1>Full Menu</h1>
                    </div>
                    {this.props.loggedIn ? <DelayLink to="/dashboard" delay={600} onDelayStart={() => this.props.runLoader({time: 750, toggle: false})}><div className="select tl"><h1>Dashboard</h1></div></DelayLink> :
                         <DelayLink to="/login" delay={600} onDelayStart={() => this.props.runLoader({time: 750, toggle: false})}><div className="select tl"><h1>Log In</h1></div></DelayLink>
                    }
                    <DelayLink to="/bookings" delay={600} onDelayStart={() => this.props.runLoader({time: 750, toggle: false})}><div className="select bl">
                        <h1>Bookings</h1>
                    </div></DelayLink>
                    <DelayLink to="/shop" delay={600} onDelayStart={() => this.props.runLoader({time: 750, toggle: false})}><div className="select br">
                        <h1>Shop</h1>
                    </div></DelayLink>
                </div>
            </div>
        )
    }
}

export default Landing
