import React, { Component } from 'react'

import { IconButton, Button, Snackbar, Dialog, DialogTitle, DialogActions } from '@material-ui/core';
import FavoriteIcon from '@material-ui/icons/Favorite';
import RemoveShoppingCartIcon from '@material-ui/icons/RemoveShoppingCart';
import AddShoppingCartIcon from '@material-ui/icons/AddShoppingCart';
import AddIcon from '@material-ui/icons/Add';
import RemoveIcon from '@material-ui/icons/Remove';
import ArrowUpwardIcon from '@material-ui/icons/ArrowUpward';
import ArrowDownwardIcon from '@material-ui/icons/ArrowDownward';

import DelayLink from '../DelayLink';

import nofav from '../../media/nofav.png';

const axios = require('axios');

export class Favorites extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
            favorites: [],
            onCart: [],
            favoritesFormatted: [],
            notification: '', 
            amount: 1,
            showAdd: false,
            selectedImage: '',
            selections: [],
            selected: 1,
        }

        this.removeFavorite = this.removeFavorite.bind(this);
        this.actuallyAdd = this.actuallyAdd.bind(this);
        this.reRender = this.reRender.bind(this);
    }

    reRender() {
        axios.get('/user').then((res) => {
            this.setState({onCart: res.data.cart}, () => {
                let favoritesFormatted = [];
                let imageCounter = 0;
    
                const imagesRendered = () => {
                    return new Promise((resolve, reject) => {
                        (function waitForImages() {
                            if (imageCounter === res.data.favorites.length) {
                                return resolve();
                            }
                            setTimeout(waitForImages, 30);
                        })()
                    })
                }
    
                for (let i = 0; i < res.data.favorites.length; i++) {
                    let image = res.data.favorites[i];
                    let img = new Image();
                    img.src = image;
                    img.onload = () => {
                        favoritesFormatted[i] = (
                            <div className={`image ${img.naturalWidth < img.naturalHeight ? 'portrait' : 'landscape'}`} key={image}>
                                <img className="favoritesImage" src={image}/>
                                <div className="captionOverlay">
                                    <p>{this.props.captions[image][0]}</p>
                                </div>
                                <IconButton className="favoriteIcon" onClick={() => this.removeFavorite(image)}>
                                    <FavoriteIcon color="error"/>
                                </IconButton>
                                <IconButton className="favoriteIcon cartIcon" onClick={() => this.toggleCart(image, img.naturalWidth < img.naturalHeight ? 'portrait' : 'landscape')}>
                                    {this.state.onCart.map(i => i.url).includes(image) ? <RemoveShoppingCartIcon color="error"/> : <AddShoppingCartIcon className="addToCart"/>}
                                </IconButton>
                            </div>
                        )
                        imageCounter++;
                    }
                }
    
                imagesRendered().then(() => {
                    this.setState({favorites: res.data.favorites, favoritesFormatted})
                })
            })
        })
    }

    componentDidMount() {
        axios.get('/user').then(res => {
            if (res.status === 200 || res.status === 304) {
                this.reRender();
            }
        }).catch(err => {
        })
    }

    toggleCart(image, orientation) {
        if (!this.state.onCart.map(i => i.url).includes(image)) {
            this.setState({amount: 1, showAdd: true, selectedImage: image, selections: orientation === 'portrait' ? ['12" x 18"', '16" x 24"', '24" x 36"', '32" x 48"'] : ['18" x 12"', '24" x 16"', '36" x 24"', '48" x 32"']})
        } else {
            axios.delete('/shop', {
                data: {
                    id: image,
                    quantity: 999,
                    size: 6,
                }
            }).then(res => {
                if (res.status === 200) {
                    let temp = this.state.onCart.splice(this.state.onCart.map(i => i.url).indexOf(image), 1);
                    this.setState({notification: 'Removed Favorite from Cart', onCart: temp, showAdd: false})
                    setTimeout(() => this.setState({notification: ''}), 4000)
                    this.props.runLoader({toggle: false, time: 750, type: 'dashboard'})
                    this.reRender();
                } else {
                }
            }).catch(err => {
    
            })
        }
    }

    changeAmount(diff) {
        if (this.state.amount + diff <= 10 && this.state.amount + diff > 0) {
            this.setState({amount: this.state.amount + diff})
        } else if (this.state.amount + diff > 10) {
            this.setState({notification: 'Maximum of 10 per print reached.'})
            setTimeout(() => this.setState({notification: ''}), 3000);
        }
    }

    changeSelection(diff) {
        this.setState({selected: this.state.selected+diff > 3 ? 3 : this.state.selected+diff < 0 ? 0 : (this.state.selected + diff)})
    }

    actuallyAdd() {
        if (this.state.amount <= 10 && this.state.amount > 0) {
            axios.post('/shop', {
                id: this.state.selectedImage,
                quantity: this.state.amount,
                price: (this.state.selected === 0 ? this.props.captions[this.state.selectedImage][2] : this.state.selected === 1 ? this.props.captions[this.state.selectedImage][2] * 1.2 : this.state.selected === 2 ? this.props.captions[this.state.selectedImage][2] * 1.4 : this.props.captions[this.state.selectedImage][2] * 2),
                size: this.state.selected,
            }).then(res => {
                if (res.status === 200 || res.status === 304) {
                    this.reRender();
                    this.setState({notification: 'Added Favorite To Cart', showAdd: false})
                    setTimeout(() => this.setState({notification: ''}), 4000)
                    this.props.runLoader({toggle: false, time: 750, type: 'dashboard'});
                }
            }).catch(err => {});
        } else if (this.state.amount > 10) {
            this.setState({notification: 'Maximum of 10 per print reached.'})
            setTimeout(() => this.setState({notification: ''}), 3000);
        }
    }

    removeFavorite(image) {
        axios.delete('/favorites', {data: {
            id: image
        }}).then(res => {
            if (res.status === 200) {
                setTimeout(() => {axios.get('/user').then(res => {
                    if (res.status === 200 || res.status === 304) {
                        this.reRender();
                    }
                }).catch(err => {
                })}, 300)
                this.setState({notification: 'Removed image from Favorites'})
                setTimeout(() => this.setState({notification: ''}), 4000)
                this.props.runLoader({toggle: false, time: 750, type: 'dashboard'});
            }
        })
    }

    render() {
        return (
            <div className="Favorites">
                <div className="container"><Button variant="contained" className="toPortfolio"><DelayLink delay={600} to="/portfolio" onDelayStart={() => this.props.runLoader({toggle: false, time: 750})}>View Full Portfolio</DelayLink></Button></div>
                {this.state.favoritesFormatted.length > 0 ? <div className="favoritesGrid">{this.state.favoritesFormatted}</div>: <div className="emptyPanel"><img className="emptyPanel" src={nofav} /><h2>Nothing in Favorites!</h2></div>}
                <div className={`addToCart shopPopUp ${this.state.showAdd ? 'visible' : 'hidden'}`}>
                    <div className="popContainer">
                        <DialogTitle>Add Item to Cart?</DialogTitle>
                        <p>Quantity: <IconButton onClick={() => this.changeAmount(-1)}><RemoveIcon/></IconButton> {this.state.amount} <IconButton onClick={() => this.changeAmount(1)}><AddIcon/></IconButton></p>
                        <p>Size:<IconButton onClick={() => this.changeSelection(-1)}><ArrowDownwardIcon/></IconButton> {this.state.selections[this.state.selected]} <IconButton onClick={() => this.changeSelection(1)}><ArrowUpwardIcon/></IconButton></p>
                        <p>Price: ${this.state.selectedImage.length > 0 && (this.state.selected === 0 ? this.props.captions[this.state.selectedImage][2] : this.state.selected === 1 ? this.props.captions[this.state.selectedImage][2] * 1.2 : this.state.selected === 2 ? this.props.captions[this.state.selectedImage][2] * 1.4 : this.props.captions[this.state.selectedImage][2] * 2)}.00</p>
                        <DialogActions className="buttons">
                            <Button variant="contained" color="secondary" onClick={() => this.setState({showAdd: false, amount: 1, selected: 1, selections: []})}>Cancel</Button>
                            <Button variant="contained" color="primary" onClick={this.actuallyAdd}>Add To Cart</Button>
                        </DialogActions>
                    </div>
                </div>
                <Snackbar
                    open={this.state.notification.length > 0}
                    anchorOrigin={{vertical: 'top', horizontal: 'center'}}
                    message={this.state.notification}
                    autoHideDuration={4000}
                />
            </div>
        )
    }
}

export default Favorites
